import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import work_pr_f_h_wave from "../images/work_pr_f_h_wave.jpg";

const related_blogs = [
  {
    url: "/education-blogs/what-are-rectifiers-its-application-and-types",
    title: "What are Rectifiers, application and its types",
  },

  {
    url: "/education-blogs/what-is-silicon-controlled-rectifier-SRC-and-voltage-rating",
    title: "What is Silicon Controlled Rectifier (SCR) and Voltage rating",
  },

];

const Content = () => {

  const meta = {
    title: " Working principle of a single-phase full wave half controlled rectifier",
    description:
      "In this article you can read about  Working principle of a single-phase full wave half controlled rectifier",
    canonical:
      "https://bangblogs.com/education-blogs/working-principal-of-a-single-phase-full-wave-half-controlled-rectifier",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "rectifiers, working principal of a single-phase, full wave half controlled rectifier",
      },
      property: {
        og_title: "Working principle of a single-phase full wave half controlled rectifier",
        og_type: "article",
        og_url:
          "https://bangblogs.com/education-blogs/working-principal-of-a-single-phase-full-wave-half-controlled-rectifier",
        og_description:
          "In this article you can read about  Working principle of a single-phase full wave half controlled rectifier",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Working principle of a single-phase full wave half controlled
          rectifier
        </h1>
        <div className=" p-5">
          <p className="text-justify">
            When a converter contains both diode and SCRs, the converter is
            termed semi-controlled or half-controlled. A fully-controlled
            rectifier circuit contains only controlled rectifiers, whereas a
            semi-controlled rectifier circuit is made up of both controlled and
            uncontrolled rectifiers. Due to the presence of diodes, the
            free-wheeling operation takes place without allowing the bridge
            output voltage to become negative. In a semi-controlled rectifier,
            control is affected only for positive output voltage, and no control
            is possible when its output voltage tends to become negative since
            it is clamped at zero volts.
          </p>
            <img src={work_pr_f_h_wave} alt=" Working principle of a single-phase full wave half controlled
          rectifier" className=" w-[100%]" />
          <p className="mt-2">
            The circuit contains two SCRs and two diodes. when source{" "}
            <span>
              V<sub>IN</sub>
            </span>{" "}
            is positive, SCR{" "}
            <span>
              T<sub>1</sub>
            </span>{" "}
            can be triggered at a firing angle called <span>&#8733;</span> and
            then current flows out of the source through SCR{" "}
            <span>
              T<sub>1</sub>
            </span>{" "}
            first, then through the load and returns via diode D<sub>2</sub>
          </p>

          <ul className="list-[circle] ml-5">
            <li className="mt-2">
              SCR{" "}
              <span>
                T<sub>1</sub>
              </span>{" "}
              and diode{" "}
              <span>
                D<sub>2</sub>
              </span>{" "}
              conducts during &#8733; &#60; ωt &#60; &pi;
            </li>

            <li className="mt-2">
              where &pi; &#60; ωt &#60; 2&pi;,{" "}
              <span>
                V<sub>IN</sub>
              </span>{" "}
              is, and SCR{" "}
              <span>
                T<sub>2</sub>
              </span>{" "}
              is normally triggered when ωt = &pi; + &#8733;
            </li>

            <li className="mt-2">
              During &nbsp; &pi; &#60; ωt (&pi; + &#8733;), the output of the
              bridge circuit would have been negative if we had used a
              fully-controlled bridge rectifier and if the current flow was
              continous. But here we have two diodes{" "}
              <span>
                D<sub>1</sub>
              </span>{" "}
              and{" "}
              <span>
                D<sub>2</sub>
              </span>{" "}
              instead of two SCRs. When the output of the bridge tends to become
              negative just after <span>Ét</span> exceeds &pi;, diode{" "}
              <span>
                D<sub>1</sub>
              </span>{" "}
              tends to get forward-biased and it starts conducting. Then diode{" "}
              <span>
                D<sub>2</sub>
              </span>{" "}
              is reverse-biased and it stops conducting.
            </li>

            <li className="mt-2">
              During &nbsp; &pi; &#60; ωt (&pi; + &#8733;), the devices in
              conduction are SCR{" "}
              <span>
                T<sub>1</sub>
              </span>{" "}
              and Diode{" "}
              <span>
                D<sub>1</sub>
              </span>{" "}
              and the output of the bridge is clamped at zero, assuming that the
              ON-state drops across devices if conduction is zero.
            </li>

            <li className="mt-2">
              During &nbsp; (&pi; + &#8733;) &#60; ωt &#60; 2&pi;, the devices
              in conduction are SCR{" "}
              <span>
                S<sub>2</sub>
              </span>{" "}
              and diode{" "}
              <span>
                D<sub>1</sub>
              </span>
              .{" "}
            </li>

            <li className="mt-2">
              SCT{" "}
              <span>
                T<sub>2</sub>
              </span>{" "}
              and diode{" "}
              <span>
                D<sub>2</sub>
              </span>{" "}
              would conduct during 0&#60; ωt &#60; &#8733;
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const WORK_P_F_F_WAVE_REC = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default WORK_P_F_F_WAVE_REC;
