import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import hacker from "../images/hacker.jpg";
import black_hat from "../images/black_hat.jpg";
import blue_hat from "../images/blue_hat.png";
import gray_hacker from "../images/gray_hacker.jpg";
import white_hat from "../images/white_hat.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-ip-address-and-types-how-to-find-my-ip-address",
    title: "What is IP Address and types of IP Address.",
  },

  {
    url: "/technology-blogs/what-is-malware-types-of-malware",
    title: "What is Malware and Types of Malware.",
  },

  {
    url: "/technology-blogs/computer-security-computer-threats-computer-crime",
    title: "Computer Security, Computer Threats, Computer Crime",
  },
];


const Content = () => {
  const meta = {
    title: "What is Hacker and Classification of Hacker",
    description:
      "In this article you can read about Hacker and Classification of Hacker.  Computer Hackers have been around for so many years. Since the computer and the Internet became widely used in the World, We have started to hear more and more about hacking.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-hacker-and-classification-of-hackers",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Hacker, Classification of Hacker, blact hat hacker, white hat hacker, grey hat hacker, blue hat hacker",
      },
      property: {
        og_title: "What is Hacker and Classification of Hacker",
        og_type: "article",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-hacker-and-classification-of-hackers",
        og_description:
          "In this article you can read about Hacker and Classification of Hacker.  Computer Hackers have been around for so many years. Since the computer and the Internet became widely used in the World, We have started to hear more and more about hacking.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Hacker and Classification of Hacker
        </h1>
        <div className=" p-5">
          <p className="mt-2 text-justify">
            Computer Hackers have been around for so many years. Since the
            Computer and the Internet became widely used in the World, We have
            started to hear more and more about hacking.
          </p>
          <p className="mt-2 text-justify">
            Traditionally, a Hacker is someone who likes to play with Software
            or Electronic Systems. Hackers enjoy Exploring and Learning how
            Computer systems operate. They love discovering new ways to work
            electronically. who finds weaknesses in a computer or computer
            network, though the term can also refer to someone with an advanced
            understanding of computers and computer networks.{" "}
            <span className="font-bold pl-1 pr-1">
              Hackers are people who try to gain unauthorized access to your
              computer.
            </span>
            the term hacker is reclaimed by computer programmers who argue that
            someone breaking into computers is better called a cracker.
          </p>
          <img src={hacker} alt="hacker" className="mt-2" />

          <h2 className="mt-4 text-xl font-bold">Classifications of Hacker</h2>
          <p className="mt-2 text-justify">
            hackers are classified according to their motive and type of work.
          </p>
          <ul className="list-[circle]">
            <li className="mt-4">
              <h3 className="text-[16px] font-bold">White hat hacker : </h3>
              <img
                src={white_hat}
                alt="White hat hacker"
                className="mt-2 w-[200px]"
              />
              <p className="mt-2 text-justify">
                The term "White hat hacker" refers to an ethical hacker, or a
                computer security expert, who specializes in penetration testing
                and in other testing methodologies to ensure the security of an
                organization's information systems. White-hat hackers are also
                called penetration testers, sneakers, red teams, or tiger teams.
                Generally, White hat hackers or ethical hackers are the good
                guys in the world of hackers.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="text-[16px] font-bold">Black hat hacker : </h3>
              <img
                src={black_hat}
                alt="Black hat hacker"
                className="mt-2 w-[200px]"
              />
              <p className="mt-2 text-justify">
                A black hat hacker is an individual with extensive computer
                knowledge whose purpose is to breach or bypass internet
                security. Black hat hackers are also known as crackers or
                dark-side hackers. The general view is that, while hackers build
                things, crackers break things. They are computer security
                hackers that break into computers and networks or also create
                computer viruses. The term “black hat” comes from old westerns
                where the bad guys usually wore black hats. A black hat hacker
                is an individual with extensive computer knowledge whose purpose
                is to breach or bypass internet security. Black hat hackers are
                also known as crackers or dark-side hackers. The general view is
                that, while hackers build things, crackers break things. They
                are computer security hackers that break into computers and
                networks or also create computer viruses. The term “black hat”
                comes from old westerns where the bad guys usually wore black
                hats.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="text-[16px] font-bold">Grey hat hacker : </h3>
              <img
                src={gray_hacker}
                alt="Grey hat hacker"
                className="mt-2 w-[200px]"
              />
              <p className="mt-2 text-justify">
                A grey hat hacker is a combination of a black hat and a white
                hat hacker. It may relate to whether they sometimes arguably act
                illegally, though in goodwill, or to show how they disclose
                vulnerabilities. They usually do not hack for personal gain or
                have malicious intentions, but may be prepared to technically
                commit crimes during their technological exploits to achieve
                better security.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="text-[16px] font-bold">Blue hat Hacker : </h3>
              <img
                src={blue_hat}
                alt="Blue hat Hacker"
                className="mt-2 w-[200px]"
              />
              <p className="mt-2 text-justify">
                A blue hat hacker is someone outside computer security
                consulting firms who is used to bug test a system before its
                launch, looking for exploits so they can be closed. Microsoft
                also uses the term BlueHat to represent a series of security
                briefing events.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const HACKER_TYPES = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default HACKER_TYPES;
