import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import internet_services from "../images/internet_services.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-ip-address-and-types-how-to-find-my-ip-address",
    title: "What is IP Address and types of IP Address.",
  },

  {
    url: "/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    title: " What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?",
  }
];

const Content = () => {
  const meta = {
    title: " What is Internet Services and types of Internet service",
    description:
      "In this article you can read about Internet Services and types of Internet service. The evolution of the Internet has been growing as fast as the thought of imagination. It offers access to a huge amount of information such as text, graphics, sound, and software technologies through a variety of services.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-internet-services-and-types-of-internet-service",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Internet Services, types of Internet service",
      },
      property: {
        og_title: "What is Internet Services and types of Internet service",
        og_type: "article",
        og_url: "https://bangblogs.com/what-is-internet-services-and-types-of-internet-service",
        og_description:
          "In this article you can read about Internet Services and types of Internet service. The evolution of the Internet has been growing as fast as the thought of imagination. It offers access to a huge amount of information such as text, graphics, sound, and software technologies through a variety of services.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Internet Services and types of Internet service
        </h1>

        <div className=" p-5">
          <p className="mt-2 text-justify">
            The evolution of the Internet has been growing as fast as the
            thought of imagination. It offers access to a huge amount of
            information such as text, graphics, sound, and software technologies
            through a variety of services. Some of the services persist from the
            early days of the Internet, while new and better services are added
            gradually as per the growing needs of Internet users. Internet
            Services are categorized into four different types.
          </p>

          <ol className="mt-2 list-decimal ml-4">
            <li>
              <p>Communication Services</p>
            </li>
            <li>
              <p>World Wide Web</p>
            </li>
            <li>
              <p>Information Retrieval Services</p>
            </li>
            <li>
              <p>Web Services</p>
            </li>
          </ol>
          <img
            src={internet_services}
            alt="internet services"
            className="mt-4"
          />

          <ul className="mt-8 list-[circle]">
            <li>
              <h2 className="text-xl font-bold">Communication Services : </h2>
              <p className="mt-2 text-justify">
                The Communication Services available on the Internet include a
                number of services that are revolutionizing both personal and
                business communication. Some of these are used to access
                different types of information and some are used to exchange
                information with individuals or groups according to their
                interests. there are various communication services, some of
                which are the following.
              </p>
              <ol className="list-decimal mt-2 ml-8">
                <li>
                  <h3 className="font-bold">Electronic mail (e-mail) : </h3>
                  <p className="mt-2 text-justify">
                    E-mail is one of the most popular Internet services. It is
                    used to send and receive electronic messages between
                    individuals anywhere in the I world via the Internet. These
                    messages not only contain text but may also consist of
                    images, audio clips, video clips, etc. This service also
                    allows you to attach files that contain text documents,
                    executable programs, spreadsheets, graphs, etc. The main
                    advantage of e-mail over the conventional mail system is
                    that the delivery of electronic mail is much easier,
                    cheaper, and faster. Moreover, the same message can be sent
                    simultaneously to a group of people. A large number of
                    e-mail programs are available which can manage messages for
                    you. Some of the most commonly used e-mail programs are MS
                    Outlook Express, Netscape Messenger, Eudora, etc.
                  </p>
                </li>

                <li className="mt-2">
                  <h3 className="font-bold">Telnet : </h3>
                  <p className="mt-2 text-justify">
                    Telnet is one of the oldest Internet services that allow you
                    to log on to a remote computer that is attached to the
                    Internet. It acts as an intermediator between your computer
                    and the remote computer that you want to access. Once the
                    connection is established, the user types the commands,
                    which are executed on the remote computer and the results
                    are displayed on the user's monitor.
                  </p>
                </li>

                <li className="mt-2">
                  <h3 className="font-bold">IRC (INTERNET Relay Chat) : </h3>
                  <p className="mt-2 text-justify">
                    IRC is a multi-user chat system that allows people from all
                    over the world to communicate simultaneously in real-time.
                    These conversations take place on hundreds of channels which
                    are virtual locations on IRC networks where users need to
                    join in order to chat with others.
                  </p>
                </li>

                <li className="mt-2">
                  <h3 className="font-bold">Mailing List : </h3>
                  <p className="mt-2 text-justify">
                    IRC is a multi-user chat system that allows people from all
                    over the world to communicate simultaneously in real-time.
                    These conversations take place on hundreds of channels which
                    are virtual locations on IRC networks where users need to
                    join in order to chat with others.
                  </p>
                </li>

                <li className="mt-2">
                  <h3 className="font-bold">Instant Messaging : </h3>
                  <p className="mt-2 text-justify">
                    Instant Messaging is online, interactive real-time
                    communication between two or more people who are connected
                    to the Internet. It has a unique combination of useful chat
                    features such as checking for the availability of the users,
                    chatting privately with a single person, sending immediate
                    messages that receive quick attention, voice telephony,
                    e-mail sending and receiving, file transfer, group chat, and
                    alert the user when his friends sign in or exits. Some of
                    the most commonly used instant messaging programs include
                    Yahoo Messenger, ICQ, America Online's Instant Messenger,
                    MSN Messenger, Jabber, etc.
                  </p>
                </li>
              </ol>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Internet Retrieval Services :{" "}
              </h2>
              <p className="mt-2 text-justify">
                The Internet is a collection of thousands of servers that
                maintain an incredible amount of information. The users can
                access information from these huge databases but there is no
                central listing of sites and the data they contain. Therefore,
                users find it very difficult to locate the desired information
                quickly. In order to overcome these problems, many information
                retrieval services have been designed which allow you to
                retrieve information in a quick manner. Most of these services
                are provided free of cost. Some of these are following
              </p>
              <ol className="list-decimal mt-2 ml-8">
                <li>
                  <h3 className="font-bold">FTP (File Transfer Protocol) : </h3>
                  <p className="mt-2 text-justify">
                    FTP (File Transfer Protocol): FTP enables users to transfer,
                    files between computers connected to the Internet. After the
                    user connects to the FTP server, he can navigate directories
                    that are accessible to FTP and look for the file he wants to
                    retrieve.
                  </p>
                </li>

                <li className="mt-2">
                  <h3 className="font-bold">Archie : </h3>
                  <p className="mt-2 text-justify">
                    There are many FTP servers on the Internet containing a huge
                    volume of information. In order to find a particular file, a
                    user has to login to each FTP server one by one to find the
                    server that contains the required file which is a very
                    daunting task. This problem is solved by Archie which is a
                    database of public FTP sites and their contents.
                  </p>
                </li>

                <li className="mt-2">
                  <h3 className="font-bold">
                    WAIS (Wide Area Information Service) :{" "}
                  </h3>
                  <p className="mt-2 text-justify">
                    WAIS is an Internet search tool that works in accordance
                    with the Client-Server principle. The WAIS client program
                    interacts with a WAIS server that enables the user to
                    specify the databases requested for search and to conduct a
                    search on the basis of keywords specified from those
                    databases and send a response back to the WAIS client
                    program in the form of a menu, listing all the files that
                    contain those keywords.
                  </p>
                </li>
              </ol>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">Web Services : </h2>
              <p className="mt-2 text-justify">
                Web services are web-based enterprise applications that are used
                for exchanging data between applications or systems. It allows
                software applications written in various programming languages
                and running on various platforms to interact smoothly with each
                other over the Internet using open XML (eXtensible Markup
                Language) based standards and transport protocols.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">World Wide Web (WWW) : </h2>
              <p className="mt-2 text-justify">
              The World Wide Web (WWW) or W3 Web is one of the most important Internet services which was developed by Timothy Berners-Lee in 1989. It is a mechanism for accessing linked documents spread over thousands of servers all over the Internet. These documents also known as webpages support not only text but also graphics, animations, audio, and videos. Each page may contain some highlighted text strings which act as a link to other related pages anywhere in the world. These links are known as hypertext links.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const INTERNET_SERVICE_TYPES = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default INTERNET_SERVICE_TYPES;
