import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import about_setting from "../images/about_setting.jpg";
import miui from "../images/miui.jpg";
import additional_setting from "../images/additional_setting.jpg";
import developer_option from "../images/developer_option.jpg";

const Content = () => {
  const meta = {
    title: "How to enable Developer option in POCO C3",
    description:
      "In this Blog you can read about How to enable Developer option in POCO C3?",
    canonical: "https://bangblogs.com/mobile-blogs/how-to-enable-developer-option-poco-c3",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Developer option, Developer option in POCO C3",
      },
      property: {
        og_title: "How to enable Developer option in POCO C3",
        og_type: "blog",
        og_url: "https://bangblogs.com/mobile-blogs/how-to-enable-developer-option-poco-c3",
        og_description:
          "In this Blog you can read about How to enable Developer option in POCO C3?",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          How to enable Developer option in POCO C3
        </h1>

        <div className=" p-5">
          <p className="text-justify">
            <span className="font-bold">Developer Options</span> on Android is a
            hidden feature you need to enable access to its various features.
            Developer options allow you to configure your device behaviors to
            improve app performance. for example,
            <span className="font-bold"> USB debugging</span> is an option in
            the developer feature that provides access you can control your
            phone through a PC with the help of a USB cable and can install an
            app through pc without touch to the phone.
          </p>

          <h2 className="text-xl font-bold mt-8">Steps to enable developer option</h2>

          <ol className="list-decimal ml-4 mt-4 font-extrabold text-lg">
            <li className="mt-4"><p className="font-light text-[16px]">Open settings in your phone and click on <span className="font-bold">About phone</span></p></li>

            <img src={about_setting} alt="poco c3 setting" className="mt-4 w-[300px]" />

            <li className="mt-4"><p className="font-light text-[16px]">click 7 times on <span className="font-bold">MIUI version(For POCO)</span></p></li>

            <img src={miui} alt="about phone poco c3"  className="mt-4 w-[300px]" />

            <li className="mt-4"><p className="font-light text-[16px]">Now developer option has enabled in your phone</p></li>

            <li className="mt-4"><p className="font-light text-[16px]">Go back and scroll down, you will found <span className="font-bold">Additional settings</span></p></li>

            <img src={additional_setting} alt="additional setting poco c3"  className="mt-4 w-[300px]"/>

            <li className="mt-4"><p className="font-light text-[16px]">click on it, now you can see <span className="font-bold">Developer options</span></p></li>

            <img src={developer_option} alt="developer setting poco c3"  className="mt-4 w-[300px]" />

          </ol>
        </div>
      </DocumentMeta>
    </>
  );
};

const DEVELOPER_OPTION_POCO_C3 = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default DEVELOPER_OPTION_POCO_C3;
