import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page";

const SPORTS_PAGE = () => {

    const {sports_page_blogs, s_text, spo_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={sports_page_blogs} se_text={s_text.sports_page_text} page_api={spo_api} dis_length={display_length.sp_p_b} page_blog_type="SPORTS_PAGE_BLOG" page_name="Sports Blogs" seperate_page_for_search="SPORTS_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default SPORTS_PAGE;
