import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import oly_p from "../images/olympic-profile.jpg";
import su_ol from "../images/summer-olympics.jpg";
import win_ol from "../images/winter-olympic.jpg";
import paralympics from "../images/paralympics.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";


const related_blogs = [
  {
    url: "/education-blogs/importance-of-healthy-lifestyle",
    title: "Importance of Healthy Lifestyle",
  },

  {
    url: "/education-blogs/introduction-about-olymic-flag-olymic-symbols",
    title: "Introduction about Olympics Flag, Symbols, Torch, Motto",
  },

];


const Content = () => {
  const meta = {
    title:
      " Introduction about olympics and types of olympics",
    description:
      "In this article you can read about olympics and types of olympics. The Olympic movement deals with understanding the development of the Olympic Games from when they started to the present.",
    canonical:
      "https://bangblogs.com/education-blogs/what-is-olympic-and-types-of-olympic",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "olympics, types of olympics, modern olympics, winter olympics",
      },
      property: {
        og_title:
          "Introduction about olympics and types of olympics",
        og_type: "article",
        og_url:
          "https://bangblogs.com/education-blogs/what-is-olympic-and-types-of-olympic",
        og_description:
          "In this article you can read about olympics and types of olympics. The Olympic movement deals with understanding the development of the Olympic Games from when they started to the present.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Introduction about olympics and types of olympics
        </h1>
        <div className=" p-5">
          <p className="text-justify">
            The Olympic movement deals with understanding the development of the
            Olympic Games from when they started to the present. The goal of the
            Olympic movement is to contribute to building a peaceful and better
            world. It includes educating the youth through the practice of
            sports in the Olympic spirit and without any discrimination. It
            requires mutual understanding with a spirit of friendship,
            solidarity, and fair play.
          </p>

          <p className="mt-2 text-justify">
            According to historical records, the first ancient Olympic Games can
            be traced back to 776 B.C. There were a series of athletic
            competitions held for representatives of various city-states of
            Ancient Greece held in honor of Zeus. They were staged on the
            ancient plains of olympics in Greece. The games were usually held
            every fourth year.
          </p>

          <img src={oly_p} alt="olympic" />

          <h2 className="text-xl font-bold mt-4">Types of olympics</h2>
          <ul className="list-[circle] ml-5 mt-2">
            <li>
              <h3 className="text-[1rem] font-bold">Modern Olympics : </h3>
              <p className="text-light text-justify mt-2">
                <span className="font-bold">Baron Pierre De Coubertin</span>{" "}
                (1863 - 1896) of France, is rightly known as the{" "}
                <span className="font-bold">
                  Founder of the Modern Olympic Games.
                </span>{" "}
                This wealthy young nobleman was commissioned by the French
                Government in 1889 to study physical culture throughtout the
                civilized world.
              </p>

              <p className="mt-2 text-justify">
                On November, 25, 1892, De Coubertin in a lecture at the Sorbonne
                in paris for the first time publicly advanced his conviction
                that there should be a modern revival of the ancient games.
              </p>

              <p className="mt-2 text-justify">
                On April 6th 1896, after a gap of 1503 years, 80,000 Athenians
                witnessed the modern Olympics.Athletics, gymnastics, wresting
                lifting, swimming, shooting, fencing, cycling and tennis were
                included in the first modern olympic games.
              </p>
            </li>
            <li className="mt-2">
              <h3 className="text-[1rem] font-bold">Summer Olympics : </h3>
              <img
                src={su_ol}
                alt="summer olympics"
                className="h-[300px] w-[100%] object-cover mt-2"
              />
              <p className="text-light text-justify mt-2">
                The Summer Olympic Games or the Games of the Olympiad first held
                in 1896, is an international multi-sport event that is hosted by
                a different city every four years. The International Olympic
                Committe organizes the game and oversees the host city's
                preperations. In each Olympic event, gold medals are awarded for
                first place, silver medals are awarded for second place, and
                bronze medals are awarded for third, this tradition began in
                1904.{" "}
              </p>

              <p className="mt-2 text-justify">
                Eighteen countries have hosted the Summer Olympics. The United
                States has hosted four Summer Olympics (1904, 1932, 1984, 1996
                ), more than any other nation.
              </p>

              <p className="mt-2 text-justify">
                Asia has hosted the Summer Olympics three times (1964, 1988,
                2008) in japan, south korea and china.
              </p>
            </li>
            <li className="mt-2">
              <h3 className="text-[1rem] font-bold">Winter Olympics : </h3>
              <img
                src={win_ol}
                alt="winter olympics"
                className="h-[300px] w-[100%] object-cover mt-2"
              />
              <p className="text-light text-justify mt-2">
                The Winter Olympic Games is a major international sporting event that occurs once every four years. Unlike the Summner Olympics feature sports practised on snow and ice. The first Winter Olympics, the 1924 Winter Olympics, was held in chamoni, France. The original five sports were bobsleigh, curling, ice hockey, Nordic skiing and Skating.
              </p>

              <p className="mt-2 text-justify">
                  The Games were held every four years from 1924 until 1936, after which they were interrupted by World War II. The Olympics resumed in 1948 and was again held four years.
              </p>

              <p className="mt-2 text-justify">
                The winter Games have evolved since its inception. Sports and disciplines have been added and some of them, such as Alpine skiing, luge, short track speed skating, freestyle skiing, skeleton, and snowboarding, have earned a permanent spot on the Olympic programme.
              </p>

              <p className="mt-2 text-justify">
                Twelve countries - Austria, Canadan, Finland, France, Great Britain, Hungary, Italy, Norway, Poland, Sweden, Switzerland and the United states - have sent athletes to every Winter Olympic Games. Six of those - Austria, Canada, Finland, Norway, Sweden and the United States - have earned medals at every Winter Olympic Games, and only one - the united states - has earned gold at each Games.
              </p>
            </li>

            <li className="mt-2">
              <h3 className="text-[1rem] font-bold">Paralympics : </h3>
              <img
                src={paralympics}
                alt="paralympics"
                className="h-[300px] w-[100%] object-cover mt-2"
              />
              <p className="text-light text-justify mt-2">
               The Paralympic Games is a major international multi-sport event, involving athletes with a range of physical disabilities, including imparied muscle power, imparied passive range of movement, limb deficiency, leg length difference, short stature, hypertonia, ataxia, athetosis,vision impairment and intellectual impairment.
              </p>

              <p className="mt-2 text-justify">
                  In 1944, at the request of the British Government, Dr. Ludwig Guttmann opened a spinal injuries centre at the Stroke Mandeville Hospital in Great Britain. Dr. Guttman's aim was to create an elite sports competition for people with disabilities that would be equalivalent to the Olympic Games.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const OLYMPICS_TYPES = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default OLYMPICS_TYPES;
