import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";


const related_blogs = [
  {
    url: "/technology-blogs/telnet-advantages-and-limitations",
    title: "What is Telnet? Advantages of Telnet and Limitations of Telnet.",
  },

  {
    url: "/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    title: " What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?",
  },
];

const Content = () => {
  const meta = {
    title: "How to Create Bootable Pendrive with CMD",
    description:
      "In this Blog you can read about How to Create Bootable Pendrive with CMD?",
    canonical:
      "https://bangblogs.com/technology-blogs/how-to-create-bootable-pendrive-with-cmd",
    meta: {
      charset: "utf-8",
      name: {
        keywords: " Create Bootable Pendrive with CMD, Bootable Pendrive",
      },
      property: {
        og_title: "How to Create Bootable Pendrive with CMD",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/how-to-create-bootable-pendrive-with-cmd",
        og_description:
          "In this Blog you can read about How to Create Bootable Pendrive with CMD?",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          How to Create Bootable Pendrive with CMD
        </h1>

        <div className=" p-5">
          <h2 className="text-xl font-bold mt-8 mb-4">
            What is Bootable Pendrive?
          </h2>
          <p className="text-justify">
            A <span className="font-bold">bootable Pendrive</span> is a drive that is used to run a system for the
            installation of an operating system. A Bootable Pendrive can be made
            through the command line and with third-party software. <span className="font-bold">A Pendrive
            made bootable using the command line, can only be used with
            windows(7/8/8.1/10).</span> To use Bootable Pendrive in other operating
            systems, you have to use third-party software.
          </p>

          <h2 className="text-xl font-bold mt-8">
            Steps to create Bootable Pendrive through CMD
          </h2>

          <ol className="list-decimal ml-4 mt-4 font-extrabold text-lg">
            <li className="mt-4">
              <p className="font-light text-[16px]">
                Run Command Prompt as <span className="font-bold">Administrator</span>
              </p>
            </li>

            

            <li className="mt-4">
              <p className="font-light text-[16px]">
              Make sure Pendrive is connected to the computer.
              </p>
            </li>

            

            <li className="mt-4">
              <p className="font-light text-[16px]">
                Now type <span className="font-bold">diskpart</span>, press Enter
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
                Now type <span className="font-bold">list disk</span>, press Enter
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
                Select your Pendrive by type <span className="font-bold">select disknumber for example select 0</span>, press Enter
              </p>
            </li>

            
            <li className="mt-4">
              <p className="font-light text-[16px]">
              type <span className="font-bold">clean</span>, press Enter
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
              type <span className="font-bold">create partition primary</span>, press Enter
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
              Now type <span className="font-bold">select partition 1</span>, press Enter
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
              type <span className="font-bold">active</span>, press Enter
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
              type <span className="font-bold">format fs = ntfs quick</span>, press Enter
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
                Now wait for some seconds and pendrive will be bootable
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
              type <span className="font-bold">exit</span>, press Enter to close the CMD
              </p>
            </li>

           
          </ol>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const BOOTABLE_PENDRIVE = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default BOOTABLE_PENDRIVE;
