import React from 'react'
import {Outlet} from 'react-router-dom';
import Nav from '../components/Nav'
import Header from '../components/Header'
import Footer from '../components/Footer'

const Layout = () => {
  return (
    <>
        <Nav></Nav>
        <Header></Header>
        <Outlet/>
        <Footer></Footer>
    </>
  )
}

export default Layout