import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-digital-subscriber-line-advantages-and-disadvantages",
    title: "What is Digital Subscriber Line, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-proxy-server",
    title: "What is Proxy Server",
  },
];

const Content = () => {
  const meta = {
    title: "What is Operating System? Functions of Operating System",
    description:
      "In this article you can read about What is Operating System? Functions of Operating System.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-operating-system-and-functions-of-operating-system",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Operating System, Functions of Operating System",
      },
      property: {
        og_title:
          "What is Operating System? Functions of Operating System",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-operating-system-and-functions-of-operating-system",
        og_description:
          "In this article you can read about What is Operating System? Functions of Operating System.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Operating System? Functions of Operating System.
        </h1>

        <div className=" p-5">
          <h2 className="text-xl font-bold mt-8">Operating System</h2>
          <p className="mt-2 text-justify">
            The operating system is a set of master programs designed to control
            the entire computer operation. This is the software that is required
            in order to run other application software. The operating system can
            be regarded as a track on which the train runs. As the track acts as
            a strong link between the earth and the train, the operating system
            also serves as a strong link between the hardware and the computer
            user.
          </p>

          <p className="mt-2 text-justify">
            To run the train, the track has to be installed first, similarly,
            the operating system is required in the system to make it
            operational. It is loaded automatically into the computer whenever
            we start the computer.
          </p>

          <p className="mt-2 text-justify">
            An operating system is general-purpose software that is involved in
            each and every activity of the computer. The operating system
            decides when and how to process a given data, where to store the
            processed data, and from where to supply the data when the user
            requests.
          </p>

          <p className="mt-2 text-justify">
            Creating an operating system is harder than an application system.
            the application system is developed for a single purpose but on the
            other hand, an operating system is developed for multiple purposes.
          </p>

          <h2 className="text-xl font-bold mt-8">
            Functions of Operating System
          </h2>

          <ol className="list-decimal mt-4 ml-4">
            <li>
              <h3 className="font-bold text-[17px]">
                Interface between User and Hardware :{" "}
              </h3>
              <p className="mt-1 text-justify">
                The computer hardware does not understand the language used by
                humans to communicate with each other. It works on the binary
                language of Os and 1s. The users work in a human-understandable
                language like English but still, we get the correct result from
                the computer. It is the operating system that converts our input
                to the language understandable by the hardware, then it converts
                the output to user understandable language.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="font-bold text-[17px]">
                Pheripheral Management :{" "}
              </h3>
              <p className="mt-1 text-justify">
                The operating system manages the working of different
                input/output devices attached to the computer. Different
                input-output devices like printers and scanners work under the
                control of the operating system. For example, If there are 3
                print commands fired from different applications, the operating
                system will ensure that all the printing jobs are performed
                efficiently and independently of each other.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="font-bold text-[17px]">Memory Managment : </h3>
              <p className="mt-1 text-justify">
                Memory management is one of the most critical jobs of the
                operating system. The operating system decides which file will
                be stored at which location in the memory. Both primary and
                secondary memories work under the control of the operating
                system. The operating system takes data needed for processing
                from secondary memory and stores it in the main memory. After
                processing, it takes the output from the main memory and stores
                it in the secondary memory when required by the user.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="font-bold text-[17px]">Process Management : </h3>
              <p className="mt-1 text-justify">
                In operating systems, allowing multiple applications to run
                simultaneously, the operating system keeps track of a number of
                processes waiting to get processed and manages the processing of
                each and every process by providing them with whatever they
                want. Modern operating systems allocate a part of the main
                memory to each running application.
              </p>
            </li>
          </ol>
        </div>
        <RELATED_BLOG_COMPONENT
          api_name={related_blogs}
        ></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const OPERATING_SYSTEM = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default OPERATING_SYSTEM;
