import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import normal_name from "../images/normal_name.jpg";
import number_name from "../images/number_name.jpg";
import dns from "../images/dns.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-digital-subscriber-line-advantages-and-disadvantages",
    title: "What is Digital Subscriber Line, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/telnet-advantages-and-limitations",
    title: "What is Telnet? Advantages of Telnet and Limitations of Telnet.",
  },
];

const Content = () => {
  const meta = {
    title: "What is Domain Name System (DNS)",
    description:
      "In this article you can read about Domain Name System (DNS),  Each host on the Internet is distinguished from the others by their unique IP addresses. An IP address is represented by a combination of numbers that are long and difficult to remember.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-domain-name-system-and-block-diagram",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Domain Name System, DNS, block diagram of DNS",
      },
      property: {
        og_title:
          "What is Domain Name System (DNS)",
        og_type: "article",
        og_url:
          "https://bangblogs.com/what-is-domain-name-system-and-block-diagram",
        og_description:
          "In this article you can read about Domain Name System (DNS), Each host on the Internet is distinguished from the others by their unique IP addresses. An IP address is represented by a combination of numbers that are long and difficult to remember.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Domain Name System (DNS)
        </h1>

        <div className=" p-5">
          <p className="mt-2 text-justify">
            Each host on the Internet is distinguished from the others by their
            unique IP addresses. An IP address is represented by a combination
            of numbers that are long and difficult to remember. However,
            remembering names is much easier, as it easier to remember a person
            by his name rather than a phone number. So instead of using numbers
            to represent a host, people prefer to use user-friendly names to
            identify each host. Although these user-friendly names make it
            easier to remember and identify each host but still they must be
            translated into their IP address before any communication takes
            place between two hosts.
          </p>

          <p className="mt-4 font-bold">For example : </p>

          <p className=" mt-2">
            If any person's name is a number is hard to remember others.
          </p>

          <img src={number_name} alt="number name of man" />

          <p className=" mt-2">
            If any person's name is a regular except for a number is easy to
            remember others.
          </p>

          <img src={normal_name} alt="regular name of man" />

          <p className="mt-2 text-justify">
            One of the earliest methods available was the Host file. It
            contained a list of host names and their corresponding IP addresses
            similar to your address directory in your mobile phones. When a user
            wanted to resolve a host name to an address, it had to download the
            host file into his system. With the increase in the number of hosts
            on the Internet, the size of the host file also increased which
            resulted in an increase of the massive amount of traffic on
            downloading this file. Moreover, every time a given computer address
            changed, every computer that wanted to communicate with it needed an
            update to its host file. So in order to resolve the problems faced
            by this system, a new system was devised known as Domain Name System
            (DNS). The DNS is a hierarchical naming scheme and a distributed
            database of IP addresses and associated names. It is distributed in
            the sense that unlike host files its data is distributed across a
            wide range of machines.
          </p>

          <img src={dns} alt="domain name server" />
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const DOMAIN_NAME_SYSTEM = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default DOMAIN_NAME_SYSTEM;
