import React, { useState } from "react";
import { Link } from "react-router-dom";

const Nav = () => {
  const [height, setHeight] = useState(0);
  const [stylen, setStylen] = useState("");
  const [click_check, setClick_check] = useState(false);
  const [time, setTime] = useState(false);
  const phonenav = () => {
    if (!click_check) {
      if (!time) {
        setHeight(200);
        setClick_check(true);
        setTimeout(() => {
          setTime(true);
          setStylen("visible");
        }, 300);
      }
    } else {
      if (time) {
        setHeight(0);
        setStylen("hidden");
        setClick_check(false);
        setTimeout(() => {
          setTime(false);
        }, 300);
      }
    }
  };

  return (
    <>
      <nav>
        <div className="main-nav-con">
          <div className="nav-con">
            <div className="com-name">
              <Link to="/">
                <h1>BANGBLOGS</h1>
              </Link>
            </div>
            <div className="menu">
              <ul>
                <Link to="/">
                  <li>Home</li>
                </Link>
                <Link to="/about">
                  <li>About</li>
                </Link>
                <li className="bl">
                  Blogs
                  <div>
                    {/* <Link to="/fashion-blogs"><div>Fashion</div></Link> */}
                    {/* <Link to="/movie-blogs">
                      <div>Movies</div>
                    </Link> */}
                    <Link to="/technology-blogs"><div>Technology</div></Link>
                    <Link to="/mobile-blogs"><div>Mobiles</div></Link>
                    {/* <Link to="/sports-blogs"><div>Sports</div></Link> */}
                    {/* <Link to="/painting-blogs"><div>Painting</div></Link> */}
                    <Link to="/education-blogs"><div>Education</div></Link>
                    <Link to="/other-blogs"><div>Others</div></Link>
                  </div>
                </li>
                <Link to="/contact">
                  <li>Contact</li>
                </Link>
              </ul>
              <button onClick={phonenav}>
                <span>
                  <i className="fa fa-bars"></i>
                </span>
              </button>
            </div>
          </div>
        </div>
        <div
          className="phone-nav"
          id="pn"
          style={{ height: height + "px", overflow: stylen }}
        >
          <ul>
            <Link to="/">
              <li>Home</li>
            </Link>
            <Link to="/about">
              <li>About</li>
            </Link>
            <li className="pbl">
              Blogs
              <div>
                {/* <Link to="/fashion-blogs">
                  <div>Fashion</div>
                </Link> */}
                {/* <Link to="/movie-blogs">
                  <div>Movies</div>
                </Link> */}
                <Link to="/technology-blogs"><div>Technology</div></Link>
                <Link to="/mobile-blogs"><div>Mobiles</div></Link>
                {/* <Link to="/sports-blogs"><div>Sports</div></Link> */}
                {/* <Link to="/painting-blogs"><div>Painting</div></Link> */}
                <Link to="/education-blogs"><div>Education</div></Link>
                <Link to="/other-blogs"><div>Others</div></Link>
              </div>
            </li>
            <Link to="/contact">
              <li>Contact</li>
            </Link>
          </ul>
        </div>
      </nav>
    </>
  );
};

export default Nav;
