import React from 'react'
import SEARCH_FOR_BLOG_PAGE from '../components/Search_for_blog_page';

import "../css/blog_page.css";
const BLOG_TEMPLATE = ({children}) => {
  return (
    <>
        <div className='blog-page-main-con'>
            <div className='sub-blog-page-main-con'>
                <div className='sub-sub-blog-page-main-con'>
                    <div className='individual-blog-con'>
                        {children}
                    </div>
                </div>
                <SEARCH_FOR_BLOG_PAGE></SEARCH_FOR_BLOG_PAGE>
            </div>
        </div>
    </>
  )
}

export default BLOG_TEMPLATE