import React from "react";
import { useNavigate } from "react-router-dom";
import { useGloabalContext } from "../pages/ContextAPI";
import Pagination from "./Pagination";

const Main = () => {

  const navigate = useNavigate();
  const { blog, technology_blog, education_blog, other_category_blog_blog, types_of_blog_length} = useGloabalContext();

  const IndividualBlog = ({blog_type, api_name, d_pages, t_pages}) => {
    return(
        <section>
        <div className="movies-main-blog-con">
          <h2>{blog_type}</h2>
          <div className="movies-blog-content-con">
            {api_name.map((curEle, id) => {
              return (
                <div className="movies-blog-con" key={id} onClick={() => navigate(curEle.url)}>
                  <div className="movies-img-con">
                    <img src={curEle.image} alt={curEle.alt_text} />
                  </div>
                  <div>
                    <h4 className="movies-blog-type">{curEle.type}</h4>
                    <h3>{curEle.title}</h3>
                    {/* <div>
                      <span>{curEle.au_ti}</span>
                    </div> */}
                  </div>
                </div>
              );
            })}
          </div>
          <Pagination display_pages={d_pages} total_pages={t_pages} specific_blog={blog_type}></Pagination>
        </div>
      </section>
    )
  }

  

  return (
    <>
      <main>
        {/* Latest blogs start */}
        <div className="main-con">
          <div className="main-content-con">
            <div className="main-text-con">
              <h2>Latest Blogs</h2>
            </div>
            <div className="main-blog-con">
              {blog.map((curEle, id) => {
                return (
                  <div key={id} onClick={() => navigate(curEle.url)}>
                    <img src={curEle.image} alt="g"></img>
                    <div className="main-blog-title">
                      <h2>{curEle.title}</h2>
                      <p>{curEle.desc}</p>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
          {/* Latest blogs end */}
        
          {/* movies blogs start */}
            <IndividualBlog d_pages={types_of_blog_length.displayTechLength} t_pages={types_of_blog_length.techLength} blog_type="TECH" api_name={technology_blog}></IndividualBlog>
          {/* movies blogs end */}

          {/* education blogs start */}
          <IndividualBlog d_pages={types_of_blog_length.displayEducationLength} t_pages={types_of_blog_length.educationLength} blog_type="EDUCATION" api_name={education_blog}></IndividualBlog>
          {/* education blogs end */}

          {/* fashion blogs start */}
          {/* <IndividualBlog d_pages={types_of_blog_length.displayMobileLength} t_pages={types_of_blog_length.mobileLength} blog_type="MOBILE" api_name={mobile_blog}></IndividualBlog> */}
          {/* fashion blogs end */}

          {/* fashion blogs start */}
          <IndividualBlog d_pages={types_of_blog_length.displayother_category_blogLength} t_pages={types_of_blog_length.other_category_blogLength} blog_type="OTHER" api_name={other_category_blog_blog}></IndividualBlog>
          {/* fashion blogs end */}

          

        </div>
      </main>
    </>
  );
};

export default Main;

