import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page"

const OTHER_BLOGS = () => {

    const {other_page_blogs, s_text, oth_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={other_page_blogs} se_text={s_text.other_page_text} page_api={oth_api} dis_length={display_length.ot_p_b} page_blog_type="OTHER_PAGE_BLOG" page_name="More Blogs" seperate_page_for_search="OTHER_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default OTHER_BLOGS;
