import { useNavigate } from "react-router-dom";


// related blogs function
const RELATED_BLOG_COMPONENT = ({ api_name }) => {

  const navigate = useNavigate();


    return (
      <>
        <hr />
        <h2 className="text-xl font-bold mt-8 ml-4 text-blue-600">Related Blogs</h2>
        {api_name.map((ele, id) => {
          return (
            <div key={id} className="mt-4 ml-4 mb-2">
              <h3 className="font-bold">{ele.title}</h3>
              <p
                onClick={() => navigate(ele.url)}
                className="mt-2 pt-1 pb-1 pl-3 pr-3 border border-gray-300 inline-block hover:bg-gray-500 hover:text-white cursor-pointer"
              >
                Read More
              </p>
            </div>
          );
        })}
      </>
    );
  };

  export default RELATED_BLOG_COMPONENT;