import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import who_control_internet from "../images/who_control_internet.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-proxy-server",
    title: "What is Proxy Server",
  },

  {
    url: "/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    title: " What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?",
  },

  {
    url: "/technology-blogs/what-is-internet-services-and-types-of-internet-service",
    title: "What is Internet Services and types of Internet service",
  },
];


const Content = () => {
  const meta = {
    title: "Who Controls the Internet",
    description:
      "In this article you can read about Who Controls the Internet, The Internet is a loosely organized international collaboration of autonomous interconnected networks. Virtually any entity can connect to the Internet to offer resources or to access them.",
    canonical:
      "https://bangblogs.com/technology-blogs/who-control-the-internet",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Who Controls the Internet, WWW, IRTF",
      },
      property: {
        og_title: "Who Controls the Internet",
        og_type: "article",
        og_url:
          "https://bangblogs.com/who-control-the-interne",
        og_description:
          "In this article you can read about Who Controls the Internet, The Internet is a loosely organized international collaboration of autonomous interconnected networks. Virtually any entity can connect to the Internet to offer resources or to access them.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Who Controls the Internet
        </h1>

        <div className=" p-5">
          <p className="mt-2 text-justify">
            The Internet is a loosely organized international collaboration of
            autonomous interconnected networks. Virtually any entity can connect
            to the Internet to offer resources or to access them. Any type of
            information can transverse the Internet without any regulatory
            interference. There is no central authority that owns the Internet.
            Many institutions and companies donate their resources in the form
            of servers and computer technicians to hold up some part of the
            Internet. Now governments around the world have also started taking
            hold through legislation. There exist a handful of organizations
            that are truly influential in various Internet administrative and
            support activities. These organizations include ISOC, IAB, IETF,
            IESG, IRTF, W3C, IANA, and ICANN.
          </p>

          <img src={who_control_internet} alt="who control internet"  className="mt-4"/>

          <ul className="mt-8 list-[circle]">
            <li>
              <h2 className="text-xl font-bold">Internet Society (ISOC) : </h2>
              <p>
                The Internet Society (ISOC) is a voluntary organization formed
                in 1991. Its mission is to assure the development, evolution,
                and use of the Internet for the benefit of people all around the
                world. It is active in the administration, censorship, and other
                policies and practices related to the Internet. It also oversees
                a number of other boards and task forces such as IAB, IETF,
                IESG, and IRTF.
              </p>
            </li>
            <li>
              <h2 className="text-xl font-bold">
                Internet Architecture Board (IAB) :{" "}
              </h2>
              <p>
                The Internet Architecture Board (IAB) originally known as the
                Internet Activities Board, is responsible for defining the
                overall architecture for the protocols and procedures used by
                the Internet. It also supervises the activities and guides two
                task forces IETF and IRTF.
              </p>
            </li>
            <li>
              <h2 className="text-xl font-bold">
                Internet Engineering Task Force (IETF) :{" "}
              </h2>
              <p>
                The Internet Engineering Task Force (IETF) concerns itself with
                the engineering and architecture of the Internet. It is a
                principal body that develops, tests, and implements new Internet
                technological standards including protocols. The proposed
                standards are published on the Internet in the form of RFCs
                (Requests for Comment).
              </p>
            </li>

            <li>
              <h2 className="text-xl font-bold">
                Internet Engineering Steering Group (IESG) :{" "}
              </h2>
              <p>
                The Internet Engineering Steering Group (IESG) approves IETF
                standards and generally manages the standard process according
                to the policies and procedures set by ISOC trustees.
              </p>
            </li>

            <li>
              <h2 className="text-xl font-bold">
                Internet Research Task Force (IRTF) :{" "}
              </h2>
              <p>
                The Internet Research Task Force (IRTF) is a self-organized
                research group that deals with long-term issues. It focuses on
                research, on topics related to Internet protocols, architecture,
                applications, and technology.
              </p>
            </li>

            <li>
              <h2 className="text-xl font-bold">
                World Wide Web Consortium (W3C) :{" "}
              </h2>
              <p>
                The World Wide Web Consortium (W3C) is one of the most important
                Internet service which was developed by Timothy Berners-Lee in
                1989. It is a mechanism for accessing linked documents spread
                over thousands of servers all over the Internet.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const WHO_CONT_INTERNET = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default WHO_CONT_INTERNET;
