import React, { useState } from "react";
import im from "../images/overlay-bottom.png";
import { Link, useNavigate} from "react-router-dom";
import { useGloabalContext } from "../pages/ContextAPI";

const Header = () => {

  const navigate = useNavigate();

  const { check_search_text } = useGloabalContext();
  const [search_val, setSearch_val] = useState("");

  const form_vali = (e) => {
    navigate("/search-blogs");
    check_search_text(search_val, "SEARCH_PAGE");
    e.preventDefault();
  }

  return (
    <>
      <header>
        <div className="head-con">
          <div className="head-content">
            <h1>Enhance Your Knowledge</h1>
            <h2>READ BLOGS</h2>
            <form onSubmit={(e) => form_vali(e)} action="/search-blogs">
              <input
                type="text"
                placeholder="Search Blogs"
                name="SEARCH_PAGE"
                onChange={(e) => setSearch_val(e.target.value)}
              />
              <Link to="/search-blogs">
                <div
                  onClick={() => {
                    check_search_text(search_val, "SEARCH_PAGE");
                  }}
                >
                  SEARCH
                </div>
              </Link>
            </form>
          </div>
        </div>
        <img src={im} alt="" />
      </header>
    </>
  );
};

export default Header;
