import React from "react";
import "../css/search.css";
import { useGloabalContext } from "./ContextAPI";
import Pagination from "../components/Pagination";
import { useNavigate} from "react-router-dom";
import SearchLatest from "../components/Search-Latest";

const Search = () => {

  const navigate = useNavigate();
  const {fil_api, search_blogs, display_length, s_text} = useGloabalContext();

  return (
    <>
      <div className="main-search-blog-con">
        <div className="search-blog-con-popular">
          {/* left side bar */}
          <div className="search-blog-con">
            <h2>Search Result for "{s_text.search_page_text}" keyword</h2>
            <div className="sub-search-blog-con">
            {search_blogs.map((curle, id) => {
                  return (
                    <div key={id} onClick={() => navigate(curle.url)}>
                      <div className="search-img-con">
                      <img src={curle.image} alt={curle.alt_text} />
                      </div>
                      <div className="search-desc-con">
                        <h3>{curle.type}</h3>
                        <p>{curle.title}</p>
                        <span>{curle.au_ti}</span>
                      </div>
                    </div>
                  );
                })}
            </div>
            <Pagination display_pages={display_length.s_p_b} total_pages={fil_api.length} specific_blog="SEARCH_BLOGS"></Pagination>
          </div>

          {/* right side bar */}
          <SearchLatest which_page="SEARCH_PAGE"></SearchLatest>
        </div>
      </div>
    </>
  );
};

export default Search;
