import React, {useState} from 'react'
import { useNavigate } from 'react-router-dom';
import { useGloabalContext } from '../pages/ContextAPI';


const SearchLatest = ({which_page}) => {
  const navigate = useNavigate();
    const {check_search_text, blog} = useGloabalContext();
    const [search_val, setSearch_val] = useState("");

  return (
    <>
        <div className="search-popular-post-con">
            <div className="search-input-con">
              <input type="text" name={which_page} onChange={(e) => setSearch_val(e.target.value)} />
              <span onClick={() => {
                check_search_text(search_val, which_page)
                }}>Search</span>
            </div>
            <div className="popular-blog-search-con">
              <h3>Latest post</h3>
              <div className="subcon-popular-blog-search-con">
                {blog.map((curle, id) => {
                  return (
                    <div key={id} onClick={() => navigate(curle.url)}>
                      <img src={curle.image} alt={curle.alt_text} />
                      <div>
                        <h4>{curle.title}</h4>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
    </>
  )
}

export default SearchLatest