import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import dial_up_connection from "../images/dial-up-connection.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-proxy-server",
    title: "What is Proxy Server",
  },

  {
    url: "/technology-blogs/what-is-domain-name-system-and-block-diagram",
    title: "What is Domain Name System (DNS)",
  },

  {
    url: "/technology-blogs/telnet-advantages-and-limitations",
    title: "What is Telnet? Advantages of Telnet and Limitations of Telnet.",
  },
];

const Content = () => {
  const meta = {
    title: "What is Dial-up connection, Advantages and Disadvantages",
    description:
      "In this article you can read about Dial-up connection, Advantages and Disadvantages.  A dial-up connection is one of the most common types of Internet connection offered by ISPs. This connection uses a telephone line to connect your computer to the Internet.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-dial-up-advantages-and-disadvantages",
    meta: {
      charset: "utf-8",
      name: {
        keywords: " Dial-up connection, Advantages of Dial-up connection and Disadvantages of Dial-up connection",
      },
      property: {
        og_title: "What is Dial-up connection, Advantages and Disadvantages",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-dial-up-advantages-and-disadvantages",
        og_description:
          "In this article you can read about Dial-up connection, Advantages and Disadvantages.  A dial-up connection is one of the most common types of Internet connection offered by ISPs. This connection uses a telephone line to connect your computer to the Internet.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Dial-up connection, Advantages and Disadvantages
        </h1>

        <div className=" p-5">
          <p className="mt-2 text-justify">
            A dial-up connection is one of the most common types of Internet
            connection offered by ISPs. This connection uses a telephone line to
            connect your computer to the Internet. In order to access the
            Internet using such a connection, a hardware device known as a modem
            is needed. A modem acts as an interface between your computer and a
            telephone line. A communication program (such as Dialup Networking)
            instructs the modem to place a telephone call to a specific phone
            number provided by an ISP, establish a connection and then connect
            your computer to the Internet.
          </p>

          <p className="mt-2 text-justify">
            In a dial-up connection, a connection is not always on. You connect
            only when you want to access the services provided by the Internet
            and when you are finished using it just disconnect.
          </p>

          <p className="mt-2 text-justify">
            The dial-up connection either uses the SLIP (Serial Line Internet
            Protocol) or PPP (Point to Point Protocol) protocols. However, most
            of the dial-up connections are PPP protocol.
          </p>

          <p className="mt-2 text-justify">
            Most ISPS support modems at a speed of up to 56 kbps, so these
            connections are basically used to read E-mail and for Internet
            browsing. However, this type of connection has problems with video
            connections, high-speed gaming, and in multimedia intensive
            websites. While using a dial-up connection to access the Internet,
            you cannot talk on the phone. Thus, a dial-up connection does not
            allow simultaneous talking and Internet access facility.
          </p>

          <img
            src={dial_up_connection}
            alt="dial-up connection"
            className="mt-4"
          />

          <ul className="mt-8 list-[circle]">
            <li>
              <h2 className="text-xl font-bold">
                Advantages of Dial-up Connection :{" "}
              </h2>

              <ol className="list-decimal mt-2 ml-8">
                <li>
                  <p className="mt-2 text-justify">
                    It is fast enough to read information and download small
                    files.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                    It is easy to setup and most widely available. Many ISPS
                    provides this type of service.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                    It is the cheapest form of Internet access in a limited way.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                    Hardware cost in establishing such a connection is minimum.
                  </p>
                </li>
              </ol>
            </li>
          </ul>

          <ul className="mt-8 list-[circle]">
            <li>
              <h2 className="text-xl font-bold">
                Disadvantages of Dial-up Connection :{" "}
              </h2>

              <ol className="list-decimal mt-2 ml-8">
                <li>
                  <p className="mt-2 text-justify">
                  Very slow connection - You have to wait several minutes for reading e-mails with large file attachments, view videos on the net, listen to music, and the appearance of web pages containing a lot of pictures, animations, etc.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                  Connection is not always on. You need to dial up every time you want to use Internet services.

                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                  while Using this connection, you cannot use the Internet and receive phone calls simultaneously. A busy signal will be received by the person who calls you.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                  The connection gets slow when multiple users use the connection.
                  </p>
                </li>
              </ol>
            </li>
          </ul>
        </div>

        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const DIAL_UP = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default DIAL_UP;
