import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page";

const TECHNOLOGY_PAGE = () => {

    const {technology_page_blogs, s_text, tech_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={technology_page_blogs} se_text={s_text.technology_page_text} page_api={tech_api} dis_length={display_length.t_p_b} page_blog_type="TECHNOLOGY_PAGE_BLOG" page_name="Technology Blogs" seperate_page_for_search="TECHNOLOGY_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default TECHNOLOGY_PAGE;
