import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import Chakrasana from "../images/Chakrasana.jpg";
import hurdler from "../images/hurdler.jpg";
import standing_stretch from "../images/standing_stretch.jpg";
import plow from "../images/plow.jpg";
import sirsasana from "../images/sirsasana.jpg";
import standing_toe from "../images/standing_toe.jpg";
import deep_knee from "../images/deep_knee.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness",
    title: "What is Physical Fitness and Components of Physical Fitness",
  },

  {
    url: "/education-blogs/importance-of-healthy-lifestyle",
    title: "Importance of Healthy Lifestyle",
  },
];


const Content = () => {
  const meta = {
    title: "Advance Stretches",
    description:
      "In this article you can read about Advance Stretches like plow, inverse, bridge, standing stretch, etc.",
    canonical:
      "https://bangblogs.com/other-blogs/advance-stretches",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Advance Stretches, stretches, plow, inverse, bridge, Chakrasana, sirsasana",
      },
      property: {
        og_title: "Advance Stretches",
        og_type: "article",
        og_url:
          "https://bangblogs.com/other-blogs/advance-stretches",
        og_description:
          "In this article you can read about Advance Stretches like plow, inverse, bridge, standing stretch, etc.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">Advance Stretches</h1>
        <div className=" p-5">
          <p className="mt-2 text-justify">
            {" "}
            The possibility of an injury depends on numerous variables,
            including an athlete's state of training, age, previous injuries,
            structural abnormalities, fatigue, and technique.
          </p>
          <p className="mt-2 text-justify">
            The stretches listed below are rated "advanced." By this rating, I
            mean they may be too advanced or dangerous for inactive and
            moderately active individuals and even for some serious or elite
            athletes as well. These stretches are highlighted here because,
            although they may not be safe for everyone, they are considered
            essential for dance, diving, figure skating, gymnastics, martial
            arts, wrestling, and yoga. In these disciplines, certain advanced
            stretches cannot be avoided.
          </p>
          <ul className="list-[circle]">
            <li className="mt-4">
              <h2 className="text-xl font-bold">The PLow : </h2>
              <img
                src={plow}
                alt="plow stretch"
                className="mt-2 w-[100%]"
              />
              <p className="mt-2 text-justify">
                The plow (stretch 236) places excessive strain on the lower back
                and disks and is potentially dangerous for those with low back
                problems. This stretch can also create a strong stretching force
                on the neck. Another problem with the plow is that it stretches
                a region frequently flexed from faulty posture and the exercise
                reinforces the faulty posture. Last, it compresses the lungs and
                heart and interferes with breathing if there is an excessive
                deposit of fat around the abdominal region. However, the plow is
                essential for those involved in judo, wrestling, some martial
                arts, and yoga. The easiest way to reduce the risk of injury is
                to learn the exercise correctly in sequential stages.
              </p>
            </li>
            <li className="mt-4">
              <h2 className="text-xl font-bold">Traditional Hurdler's Strech : </h2>
              <img
                src={hurdler}
                alt="Traditional Hurdler's Strech"
                className="mt-2"
              />
              <p className="mt-2 text-justify">
              The traditional hurdler's stretch is designed to stretch the hamstrings along with the lower back muscles and related soft tissues. It also stretches the medial ligaments of the knee. For some individuals, this stretch promotes knee instability and twists and compresses the kneecap, which can cause the kneecap to side-slip. This stretch is commonly recommended for improving performance and preventing or rehabilitating injuries. The easiest way to reduce the risk of injury is to have the flexed leg positioned so that the outer side of the thigh and calf can rest on the floor, with the heel against the inner side of the opposite thigh.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Deep Knee Bend : 
              </h2>
              <img
                src={deep_knee}
                alt=" Deep Knee Bend"
                className="mt-2"
              />
              <p className="mt-2 text-justify">
              The deep knee bend (stretch 40), lunge, or squat (with or without weights) can endanger the lateral ligaments of the knee, compress the kneecap, and pinch and damage the cartilage in the knee. This stretch is a basic component of numerous skills in baseball, dance, gymnastics, handball, weight lifting, and wrestling. Minimize the degree of risk by reducing the velocity and depth of the descent and maintaining the knees over the long axis of the foot.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Standing Torso Twist :{" "}
              </h2>
              <img
                src={standing_stretch}
                alt="Standing Torso Twist"
                className="mt-2"
              />
              <p className="mt-2 text-justify">
              The standing torso twist (with or without weights, stretch 225) can strain the ligaments of the knee and incorporate momentum that often exceeds the absorbing capacity of the tissues being stretched. This exercise is used in many disciplines, including baseball, discus, golf, tennis, and the javelin throw. Risk reduction strategies include placing the hands on the hips, sitting in a chair, or slightly flexing the knees when standing.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
              Straight-Leg Stand and Toe Touch :{" "}
              </h2>
              <img
                src={standing_toe}
                alt=" Straight-Leg Stand and Toe Touch"
                className="mt-2 w-[100%]"
              />
              <p className="mt-2 text-justify">
              The straight-leg stand and toe touch (straddle stretch 75) stresses the medial aspect of the knees, forces the knee to hyperextend, places more significant pressure on the lumbar spine, and can result in permanent deformity, both as loose and knock-knees. This exercise is essential in several sports and disciplines such as 3- and 10-meter diving, gymnastics, powerlifting, weight lifting, and yoga.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">Bridge : </h2>
              <img src={Chakrasana} alt="Bridge, Chakrasana" className="mt-2" />
              <p className="mt-2 text-justify">
              The bridge (stretch 187) squeezes the spinal disks and pinches the nerve fibers. In addition, there is concern that repetitive bridging can cause spondylolysis or low back pain. The rationale for incorporating a bridge in a training program is that it requires skill in many disciplines such as acrobatics, gymnastics, judo, and wrestling. Risk-reduction strategies include proper sequential learning through lead-up drills and exercises, using an optimal technique, and the assistance of a knowledgeable spotter.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Inversion :{" "}
              </h2>
              <img src={sirsasana} alt="Inversion, sirsasana" className="mt-2" />
              <p className="mt-2 text-justify">
              Inversion (stretch 206) raises blood pressure, may rupture blood vessels—especially in the eyes, and may injure those with spinal instability. Inversion exercises have been hypothesized to relieve or prevent low back pain, hence they are used in rehabilitation.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const ADVANCE_STRECH = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default ADVANCE_STRECH;
