import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page"

const EDUCATION_PAGE = () => {

    const {education_page_blogs, s_text, edu_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={education_page_blogs} se_text={s_text.education_page_text} page_api={edu_api} dis_length={display_length.ed_p_b} page_blog_type="EDUCATION_PAGE_BLOG" page_name="Education Blogs" seperate_page_for_search="EDUCATION_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default EDUCATION_PAGE;
