import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page";

const MOVIES_PAGE = () => {

    const {movie_page_blogs, s_text, mov_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={movie_page_blogs} se_text={s_text.movie_page_text} page_api={mov_api} dis_length={display_length.m_p_b} page_blog_type="MOVIE_PAGE_BLOG" page_name="Movie Blogs" seperate_page_for_search="MOVIE_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default MOVIES_PAGE;
