import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page"

const FASHION_PAGE = () => {

    const {other_category_blog_page_blogs, s_text, fas_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={other_category_blog_page_blogs} se_text={s_text.other_category_blog_page_text} page_api={fas_api} dis_length={display_length.f_p_b} page_blog_type="other_category_blog_PAGE_BLOG" page_name="Other Blogs" seperate_page_for_search="other_category_blog_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default FASHION_PAGE;
