import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-ip-address-and-types-how-to-find-my-ip-address",
    title: "What is IP Address and types of IP Address.",
  },

  {
    url: "/technology-blogs/mobile-telephony-and-evolution-of-mobile-telephony",
    title: "Mobile telephony and Evolution of Mobile telophony",
  },


];


const Content = () => {
  const meta = {
    title: "What is POP (Post Office Protocol) and IMAP (Internet Message Access Protocol) and Differences",
    description:
      "In this Blog you can read about POP (Post Office Protocol) and IMAP (Internet Message AccessProtocol) and Differences. The Post Office Protocol (POP) was first proposed in 1984, then updated by POP 2 in 1988 and the current standard is POP3. It is a receive-only protocol that is designed to work in offline access mode.",
    canonical: "https://bangblogs.com/technology-blogs/what-is-post-office-protocal-and-internet-message-access-protocal-and-differences",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Proxy Server, Advantages of proxy server",
      },
      property: {
        og_title: "POP (Post Office Protocol), IMAP (Internet Message Access Protocol), Differences of POP and IMAP",
        og_type: "blog",
        og_url: "https://bangblogs.com/technology-blogs/what-is-post-office-protocal-and-internet-message-access-protocal-and-differences",
        og_description:
          "In this Blog you can read about POP (Post Office Protocol) and IMAP (Internet Message AccessProtocol) and Differences. The Post Office Protocol (POP) was first proposed in 1984, then updated by POP 2 in 1988 and the current standard is POP3. It is a receive-only protocol that is designed to work in offline access mode.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is POP (Post Office Protocol) and IMAP (Internet Message Access
          Protocol) and Differences
        </h1>

        <div className=" p-5">
          <h2 className="text-xl font-bold mt-5">POP (Post Office Protocol)</h2>
          <p className="mt-2 text-justify">
            The Post Office Protocol (POP) was first proposed in 1984, then
            updated by POP 2 in 1988 and the current standard is POP3. It is a
            receive-only protocol that is designed to work in offline access
            mode. It enables users to move their emails from the mail server to
            their computers. Operating much like a mailbox at a real post
            office, POP automatically collects/downloads all your pending mail
            from the mail server to the user's own computer, each time your
            e-mail client connects to the mail server. Once emails are delivered
            from the mail server (optionally). Thereafter, all the mail
            processing is done locally in local to the user's computer.
          </p>

          <p className="mt-2 text-justify">
            The POP protocol allows an email client to connect to any e-mail
            server on the Internet to perform usual e-mail functions such as
            reading etc. as long as they have a valid username and password. POP
            allows the mail client to communicate with the mail server by
            providing commands that generally specify the user account name and
            password.
          </p>

          <p className="mt-2 text-justify">
            The POP users require less Internet usage time and consume minimum
            server resources as users can download their emails, disconnect from
            the Internet, and read and compose messages offline.
          </p>

          <h2 className="text-xl font-bold mt-5">
            IMAP (Internet Message Access Protocol)
          </h2>
          <p className="mt-2 text-justify">
            The Internet Message Access Protocol (IMAP) is the most recent and
            much-improved protocol for accessing mail. It was first proposed in
            1986 followed by IMAP2 which came in 1987.
          </p>

          <p className="mt-2 text-justify">
            IMAP is a client-server mail protocol that permits a client program
            to manipulate e-mail messages on the server without downloading them
            on the local computer. Your e-mail is received and held for you by
            the remote server. You can view just the heading and the sender of
            each mail message and then decide whether to download the mail,
            delete it or take some other action. You can also create, manipulate
            and delete remote message folders called mailboxes. It also provides
            the provision for searching.
          </p>

          <p className="mt-2 text-justify">
            Using IMAP, messages stored in mailboxes can be accessed and managed
            from more than one computer. Thus a user can access messages from a
            workstation in the office, from a PC at home, or from a laptop when
            on a business trip. It also allows concurrent access to multiple
            mailboxes on multiple mail servers.
          </p>

          <h2 className="text-xl font-bold mt-5">
            Difference Between POP and IMAP
          </h2>

          <div className="flex">
            <div className="p-2 mr-2 ml-2 box-border">
              <div className=" font-bold">POP</div>
              <div>
                <ol className="list-decimal">
                  <li className="mt-4">
                    <p>POP is generally used to support single client.</p>
                  </li>
                  <li className="mt-4">
                    <p>POP requires less Internet usage time.</p>
                  </li>
                  <li className="mt-4">
                    <p>All the messages have to downloaded.</p>
                  </li>
                  <li className="mt-4">
                    <p>It requires minimum use of server resources.</p>
                  </li>
                  <li className="mt-4">
                    <p>Not suitable for accessing non-email data.</p>
                  </li>
                </ol>
              </div>
            </div>
            <div className="p-2 box-border">
              <div className=" font-bold">IMAP</div>
              <div>
                <ol className="list-decimal">
                  <li className="mt-4">
                    <p>IMAP is desinged to handle multiple clients.</p>
                  </li>
                  <li className="mt-4">
                    <p>IMAP requires more Internet usage time.</p>
                  </li>
                  <li className="mt-4">
                    <p>It allows selective transfer of messages to the client.</p>
                  </li>
                  <li className="mt-4">
                    <p>Clients are totally dependent on Server.</p>
                  </li>
                  <li className="mt-4">
                    <p>Suitable for accessing non-email data attachments</p>
                  </li>
                </ol>
              </div>
            </div>
          </div>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const POP_IMAP_DIFFERENCE = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default POP_IMAP_DIFFERENCE;
