import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import bluethooth from "../images/bluethooth.jpg";
import wifi from "../images/wifi.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-digital-subscriber-line-advantages-and-disadvantages",
    title: "What is Digital Subscriber Line, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-domain-name-system-and-block-diagram",
    title: "What is Domain Name System (DNS)",
  },

];


const Content = () => {
  const meta = {
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
    description:
      "In this article you can read about What is Wireless Internet Connection, Advantages and Disadvantages.  Wireless Internet is the broadband Internet connection that uses radio frequency bands to connect to the Internet instead of using a telephone line or a cable network.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Wireless Internet Connection, Advantages of WIC and Disadvantages of WIC, wifi, bluetooth",
      },
      property: {
        og_title: "What is Wireless Internet Connection, Advantages and Disadvantages",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
        og_description:
          "In this article you can read about What is Wireless Internet Connection, Advantages and Disadvantages.  Wireless Internet is the broadband Internet connection that uses radio frequency bands to connect to the Internet instead of using a telephone line or a cable network.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Wireless Internet Connection, Advantages and Disadvantages
        </h1>

        <div className=" p-5">
          <p className="mt-2 text-justify">
            Wireless Internet is the broadband Internet connection that uses
            radio frequency bands to connect to the Internet instead of using a
            telephone line or a cable network. This type of connection provides
            a very high speed, usually more than 10 Mbps, and costs considerably
            less than the equivalent of other wired versions. Its speeds range
            from 256 Kbps to 1 Gbps. It is possible to use this type of
            connection to transmit data over long distances. This type of
            connection is used by business travelers and persons who are away
            from home as long as they are within the network's coverage area.
          </p>

          <ul className="list-[circle] ml-5 mt-4">
            <li>
              <h2 className="text-xl font-bold">
                Wi-Fi (wireless fidelity) :{" "}
              </h2>
              <p className="mt-2 text-justify">
                A number of ISPs have placed 802.11b (wi-fi) network standards
                in public places such as hostels, coffee shops, airports, and
                convention centers. These networks provide easy access to the
                Internet for anyone with a compatible device (e.g. Laptop or
                PDA).
              </p>
            </li>

            <img src={wifi} alt="wifi" className="mt-4 w-[200px]" />

            <li className="mt-4">
              <h2 className="text-xl font-bold">Bluetooth : </h2>
              <p className="mt-2 text-justify">
                It is also used for Internet access although it doesn't have
                potential of the wi-fi due to its limited distance. It was
                primarily promoted as it eliminated the need of cables between
                devices such as keyboard, computers, cellular phones, Airpods,
                PDA's etc. The current standard allows upto 8 devices to be
                connected to it.
              </p>
            </li>

            <img src={bluethooth} alt="bluethooth" className="mt-4 w-[200px]" />
          </ul>

          <ul className="mt-8 list-[circle]">
            <li>
              <h2 className="text-xl font-bold">
                Advantages of Wireless Internet Connection :{" "}
              </h2>

              <ol className="list-decimal mt-2 ml-8">
                <li>
                  <p className="mt-2 text-justify">
                    It is a high-speed connection.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                    It provides the tremendous advantage of mobility and covers
                    a very long distance.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                    It provides al always-on connection.
                  </p>
                </li>
              </ol>
            </li>
          </ul>

          <ul className="mt-8 list-[circle]">
            <li>
              <h2 className="text-xl font-bold">
                Disadvantages of Wireless Internet Connection :{" "}
              </h2>

              <ol className="list-decimal mt-2 ml-8">
                <li>
                  <p className="mt-2 text-justify">
                    The initial cost of setup is very high.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                    Depending upon the signal quality, its speed varies.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                    The signal could be hacked by hackers.
                  </p>
                </li>
              </ol>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const WIRELESS_INTER_CONN = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default WIRELESS_INTER_CONN;
