import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import flag from "../images/flag.jpg";
import symbol from "../images/oly_f_symbol.png";
import oly_torch from "../images/olympic_torch.jpg";
import oly_moto from "../images/oly_moto.png";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/education-blogs/importance-of-wellness",
    query_str: "Importance of Wellness", 
    title: "Importance of Wellness",
  },

  {
    url: "/education-blogs/what-is-olympic-and-types-of-olympic",
    title: "Introduction about olympics and types of olympics",
  },

];


const Content = () => {
  const meta = {
    title: "  Introduction about Olympics Flag, Symbols, Torch, Motto",
    description:
      "In this article you can read about Olympics Flag, Symbols, Torch, Motto. The Olympic flag was created under the guidance of Bardon Coubertin in 1913 and was released in 1914.",
    canonical: "https://bangblogs.com/education-blogs/introduction-about-olymic-flag-olymic-symbols",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Olympics Flag, Olympics Symbols, Olympics Torch, Olympics Motto",
      },
      property: {
        og_title: "Introduction about Olympics Flag, Symbols, Torch, Motto",
        og_type: "article", 
        og_url: "https://bangblogs.com/education-blogs/introduction-about-olymic-flag-olymic-symbols",
        og_description:
          "In this article you can read about Olympics Flag, Symbols, Torch, Motto. The Olympic flag was created under the guidance of Bardon Coubertin in 1913 and was released in 1914.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Introduction about Olympics Flag, Symbols, Torch, Motto.
        </h1>
        <div className=" p-5">
          <ol className="list-[circle]">
            <li>
              <h2 className="text-xl font-bold">Olympic Flag</h2>
              <img src={flag} alt="Olympic flag" />
              <p className="mt-2 text-justify">
                The Olympic flag was created under the guidance of Bardon
                Coubertin in 1913 and was released in 1914. But it was first
                hoisted in 1920 in Belgium at the Antwerp Olympic games in the
                main stadium. The flag consists of a white field bearing five
                equal interlocking rings of blue, dark, yellow, black, green,
                and red with separations wherever two rings intersect. The
                width-to-length ratio of the flag is 2:3.
              </p>

              <p className="mt-2 text-justify">
                The five rings symbolized the "five parts of the world " in
                which the Olympic movement was active, according to Coubertin.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">Olympic symbols</h2>
              <img src={symbol} alt="Olympic flag symbol" />
              <p className="mt-2 text-justify">
                According to the Olympic Charter, Rule 8, The Olympic symbol is
                defined as, The Olympic symbol consisting of five interlaced
                rings of equal dimensions, used alone, in one or five different
                colors. When used in its five-color version these colors shall
                be, from left to right, blue, yellow, black, green, and red. The
                rings are interlaced from left to right, the blue, black, and
                red rings are situated at the top, and the yellow and green
                rings are at the bottom in accordance with the following graphic
                reproduction.
              </p>

              <p className="mt-2 text-justify">
                In other words, the Olympic symbol expresses the activity of the
                Olympic Movement and represents the union of the five
                continents.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">Olympic Torch</h2>
              <img src={oly_torch} alt="Olympic Torch" className="mt-2 w-[100%] object-contain h-[350px]"/>
              <p className="mt-2 text-justify">
              The Olympic flame was first introduced at the 1928 Olympics in Amsterdam. The Olympic torch is ignited several months before the opening ceremony of the Olympic games at the site of the ancient Olympics in Olympia, Greece. After being lit, the flame continues to burn throughout the Olympics and is extinguished on the day of the closing ceremony.
              </p>
            </li>
            
            
            <li className="mt-4">
              <h2 className="text-xl font-bold">Olympic motto</h2>
              <img src={oly_moto} alt="Olympic moto" className="mt-2 w-[100%] object-contain h-[350px]"/>
              <p className="mt-2 text-justify">
              The Olympic motto is made up of three Latin words: "Citus, Altius, Fortius" which means "Faster, Higher, Stronger". The three Latin words became the Olympic motto in 1894 when International Olympic Committee was created. Pierre De Coubertin proposed the motto and borrowed it from his friend Henri Didon, who was a priest and taught sports to students. These three words encourage the athletes to give their best during competition and to view this effort as a victory in itself.
              </p>
            </li>
          </ol>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const OLYMPIC_FLAG = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default OLYMPIC_FLAG;
