import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Pagination from "../components/Pagination";
import SearchLatest from "../components/Search-Latest";
import "../css/movies.css"

const INDIVIDUAL_BLOG_PAGE_COMPONENT = ({mov_pa_bl, se_text, page_api, dis_length, page_blog_type, page_name, seperate_page_for_search}) => {

  const navigate = useNavigate();

  const [set_style, setSet_style] = useState({});

    useEffect(()=>{
        if(page_api.length<=1){
            setSet_style({gridTemplateColumns: "50%"});
        }
        else{
            setSet_style({gridTemplateColumns: "45% 45%"});
        }
    }, [page_api.length])

  return (
    <>
      <div className="main-movie-blog-con">
        <div className="movie-blog-con-popular">

          {/* left side bar start */}
          <div className="movie-blog-con">
            <h1>{page_name}</h1>
            <h2>Search Result for {se_text}</h2>
            <div className="sub-movie-blog-con" style={set_style}>
                {
                    mov_pa_bl.map((curEle, id) => {
                        return(
                        <div className="movie-page-individual-blog-con" key={id} onClick={()=>navigate(curEle.url)}>
                            <div className="movie-page-img-con">
                                <img src={curEle.image} alt={curEle.alt_text} />
                            </div>
                            <div className="movie-page-desc-con">
                                <p>{curEle.type}</p>
                                <h3>{curEle.title}</h3>
                                {/* <span>{curEle.au_ti}</span> */}
                            </div>
                        </div>
                        )
                    })
                }
            </div>
            <Pagination display_pages={dis_length} total_pages={page_api.length} specific_blog={page_blog_type}></Pagination>
          </div>
          {/* left side bar end */}

          {/* right side bar start */}
          <SearchLatest which_page={seperate_page_for_search}></SearchLatest>
          {/* right side bar end */}
        </div>
      </div>
    </>
  );
};

export default INDIVIDUAL_BLOG_PAGE_COMPONENT;


