import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import ftp_profile from "../images/ftp_profile.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-proxy-server",
    title: "What is Proxy Server",
  },

  {
    url: "/technology-blogs/telnet-advantages-and-limitations",
    title: "What is Telnet? Advantages of Telnet and Limitations of Telnet.",
  },
];


const Content = () => {
  const meta = {
    title: "What is FTP (File Transfer Protocol) and Advantages, Disadvantages | and How FTP works ?",
    description:
      "In this Blog you can read FTP (File Transfer Protocol) | and How FTP works ? FTP is an abbreviation for File Transfer Protocol and is a part of the TCP/IP suite.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "FTP (File Transfer Protocol), How FTP works ",
      },
      property: {
        og_title: "What is FTP (File Transfer Protocol) | and How FTP works ?",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
        og_description:
          "In this Blog you can read FTP (File Transfer Protocol) | and How FTP works ? FTP is an abbreviation for File Transfer Protocol and is a part of the TCP/IP suite.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?
        </h1>

        <div className=" p-5">
          <h2 className="text-xl font-bold mt-5">
            FTP (File Transfer Protocol)
          </h2>
          <p className="mt-2 text-justify">
            FTP is an abbreviation for File Transfer Protocol and is a part of
            the TCP/IP suite. Its existence is as old as the Internet itself.
            The first version of FTP dates back to the early 1970s and is still
            commonly used on the Internet. In the early days of the Internet,
            several programs such as e-mail existed for moving files from one
            computer to another. But these programs did most of their work
            behind the scenes without any user interaction. As companies and
            research institutions began to store and share their data on
            computers, a method of data warehousing came into existence. FTP was
            created as a way for an individual to contact those computers and
            retrieve those files interactively. FTP allowed people on the
            network to browse through directories of files, select what they
            wanted and retrieve those files to their own computers. Besides
            downloading a file, FTP can also be used to upload files from your
            computer to another computer on the Internet.
          </p>

          <p className="mt-2 text-justify">
            FTP Server allows you to download and upload files that can be of a
            wide range of file types and formats such as ASCII, EBCDIC, and
            binary files. Some files on FTP servers are available for everyone,
            while some have authorized access.
          </p>

          <h2 className="text-xl font-bold mt-5">How FTP works ?</h2>

          <ol className="list-decimal ml-4">
            <li className="mt-3">
              <p className="text-justify">
                FTP works on a Client-Server approach.
              </p>
            </li>
            <li className="mt-3">
              <p className="text-justify">
                Whenever an FTP client wants to download files from a remote FTP
                Server it contacts the server which prompts the user to enter a
                username and a password.
              </p>
            </li>
            <li className="mt-3">
              <p className="text-justify">
                After you have specified the username and password, a successful
                connection known as a command link is established between the
                client and the server. Now, the FTP Server program is ready to
                serve the client's request for the File Transfer.
              </p>
            </li>
            <li className="mt-3">
              <p className="text-justify">
                FTP Server displays a large number of directories or folders.
                After selecting a particular folder from where you want to
                download a file, a second connection known as a data connection
                is opened up which can transfer files either in ASCII mode or
                binary mode. After the file is downloaded, the data connection
                is closed automatically but the other command link remains open.
              </p>
            </li>
          </ol>

          <img src={ftp_profile} alt="FTP working" className="mt-4" />

          <h2 className="text-xl font-bold mt-5">Advantages of FTP</h2>
          <ol className="list-decimal ml-4">
            <li className="mt-3">
              <p className="text-justify">
                Any FTP client running on a Microsoft Windows, Linux, and other
                operating system can use the FTP Server.
              </p>
            </li>
            <li className="mt-3">
              <p className="text-justify">
                FTP can be used to upload or download file to a central of
                server anywhere in the world.
              </p>
            </li>
            <li className="mt-3">
              <p className="text-justify">
                It is very simple, fast and reliable to transfer files from FTP
                server.
              </p>
            </li>
          </ol>

          <h2 className="text-xl font-bold mt-5">Disadvantages of FTP</h2>
          <ol className="list-decimal ml-4">
            <li className="mt-3">
              <p className="text-justify">
                FTP has little security protection when performing file
                transfer. The login information that the user enters before
                connecting is sent unencrypted across the network. This login
                information (i.e username and password) can be used by an
                unauthorized user to access other systems.
              </p>
            </li>
            <li className="mt-3">
              <p className="text-justify">
                If the FTP client is accessing FTP Server using command-line
                software then it needs to be aware of the working of the Unix
                operating system as most of the FTP Servers are Unix based.
              </p>
            </li>
            <li className="mt-3">
              <p className="text-justify">
                It is not always user-friendly. To retrieve a file, the user
                needs to know which FTP Server it is on and the directory where
                it is located along with the specific filename of the file.
              </p>
            </li>
          </ol>
        </div>
        <RELATED_BLOG_COMPONENT  api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const FTP_FILE_TRANSFER_PROTOCAL = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default FTP_FILE_TRANSFER_PROTOCAL;
