import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";


const related_blogs = [
  {
    url: "/technology-blogs/what-is-cellular-network-and-woking-of-cellular-telephony",
    title: "What is Cellular Network? Working of Cellular Telephony",
  },

  {
    url: "/technology-blogs/what-is-post-office-protocal-and-internet-message-access-protocal-and-differences",
    title: "What is POP (Post Office Protocol) and IMAP (Internet Message Access Protocol) and Differences",
  },

];


const Content = () => {
  const meta = {
    title: "Mobile telephony and Evolution of Mobile telophony",
    description:
      "In this Blog you can read about Mobile telephony and Evolution of Mobile telophony.",
    canonical:
      "https://bangblogs.com/technology-blogs/mobile-telephony-and-evolution-of-mobile-telephony",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Mobile telephony, Evolution of Mobile telophony",
      },
      property: {
        og_title: "Mobile telephony and Evolution of Mobile telophony",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/mobile-telephony-and-evolution-of-mobile-telephony",
        og_description:
          "In this Blog you can read about Mobile telephony and Evolution of Mobile telophony.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Mobile telephony and Evolution of Mobile telophony.
        </h1>

        <div className=" p-5">
          <h2 className="text-xl font-bold mt-8 mb-4">Mobile telophony</h2>
          <p className="text-justify">
            mobile telephones can be broken into four periods. The first
            (pre-cellular) the period involved mobile telephones that
            exclusively used a frequency band in a particular area. These
            telephones had severe problems with congestion and call completion.
            If one customer was using a particular frequency in a geographic
            area, no other customer could make a call on that same frequency.
          </p>

          <h2 className="text-xl font-bold mt-8 mb-4">
            Evolution of Mobile Telephony
          </h2>

          <ol className="list-decimal ml-4 mt-4 font-extrabold text-lg">
            <li className="mt-4">
              <h3>First Generation (1G) : </h3>
              <p className="font-light text-[16px] mt-2">
                First-generation cellular mobile telephones developed around the
                world using different, in-compatible analog technologies. These
                systems used analog modulation techniques like FM. The systems
                use frequency division multiplexing to divide the bandwidth into
                specific frequencies that are assigned to individual calls.
              </p>

              <p className="font-light text-[16px] mt-2">
                Examples of first-generation cellular systems are AMPS (Advanced
                Mobile Telephone System), NMT (Nordic Mobile Telephone) system,
                etc.
              </p>

              <p className="font-light text-[16px] mt-2">
                These systems suffered from limitations like Low service
                quality, Long call setup time, Inefficient use of bandwidth,
                Susceptible to interference, etc.
              </p>
            </li>

            <li className="mt-4">
              <h3>Second Generation (2G) : </h3>
              <p className="font-light text-[16px] mt-2">
                These second-generation systems are digital and use either TDMA
                (Time Division Multiple Access) or CDMA (Code Division Multiple
                Access) access methods.
              </p>

              <p className="font-light text-[16px] mt-2">
                Some of these systems use a combination of TDMA and FDMA
                techniques to increase the number of channels. One such example
                is GSM (Global System for Mobile) which has gained wide
                popularity and is used in most of the world today. Another
                technology in use is CDMA.
              </p>

              <p className="font-light text-[16px] mt-2">
                These systems provided better service quality than that
                first-generation cellular systems and use the given bandwidth
                more efficiently. These systems support data, speech, and
                facsimile services. The 2G digital services provided very useful
                features like expanded capacity, caller ID, call Forwarding, and
                short messaging.
              </p>
            </li>

            <li className="mt-4">
              <h3>Third Generation (3G) : </h3>
              <p className="font-light text-[16px] mt-2">
                The Third Generation (3G) System provides both speech and data
                at a very high speed. Though the second-generation system
                provides data transmission, the data rate offered is quite low
                and is unsuitable for today's needs.
              </p>

              <p className="font-light text-[16px] mt-2">
                3G networks use a variety of wireless network technologies,
                including GSM, CDMA, TDMA, WCDMA, CDMA2000, UMTS, and EDGE, and
                this leads to some confusion as well as a great deal of
                flexibility.
              </p>

              <p className="font-light text-[16px] mt-2">
                The third-generation systems integrate all kinds of services
                like speech, data, audio, video, facsimile, etc. through mobile
                handsets. These systems provide much better service quality than
                the second-generation systems and provide much smaller call set
                up delays.
              </p>
            </li>

            <li className="mt-4">
              <h3>Fourth Generation (4G) : </h3>
              <p className="font-light text-[16px] mt-2">
                before the 4G network, we can see 2.5G and 3.5G networks also
                but these networks did not get popular.
              </p>

              <p className="font-light text-[16px] mt-2">
              4G will offer true high-speed data services. 4G data rates will be in the 4-Mbit/sec to 156-Mbit/sec range, and possibly higher. 4G will also fully support IP. High data rates are due to advances in signal processors, new modulation techniques, and smart antennas that can focus signals directly at users.
              </p>

            </li>
          </ol>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const MOBILE_TELEPHONY = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default MOBILE_TELEPHONY;
