import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import proxy_server from "../images/proxy_server.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-ip-address-and-types-how-to-find-my-ip-address",
    title: "What is IP Address and types of IP Address.",
  },

  {
    url: "/technology-blogs/mobile-telephony-and-evolution-of-mobile-telephony",
    title: "Mobile telephony and Evolution of Mobile telophony",
  },


];

const Content = () => {
  const meta = {
    title: "What is Proxy Server",
    description:
      "In this article you can read about Proxy Server. A Proxy Server is software running on a gateway computer positioned between your LAN and the Internet.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-proxy-server",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Proxy Server, Advantages of proxy server",
      },
      property: {
        og_title: "What is Proxy Server",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-proxy-server",
        og_description:
          "In this article you can read about Proxy Server. A Proxy Server is software running on a gateway computer positioned between your LAN and the Internet.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Proxy Server
        </h1>

        <div className=" p-5">
          <p className="mt-2 text-justify">
          A Proxy Server is software running on a gateway computer positioned between your LAN and the Internet. The main functions provided by the proxy server are the firewalls and filtering, sharing Internet Connection, and Caching.
          </p>

          <p className="mt-2 text-justify">
          Proxy Servers are often used to provide more secure web access in the organization as they allow web browsers to receive web pages from the web servers without communicating with them directly. When a user's web browser wants to access information from a web server on the Internet, then instead of talking directly to the web server it first talks to the proxy server which then decides whether the request is to be passed to the web server or to simply ignore it. If the request is approved, the proxy server communicates with the web server on the Internet on behalf of the web browser. After getting the response from the remote web server, the proxy server sends the reply back to the web browser.
          </p>

          <img src={proxy_server} alt="proxy server" className="mt-4" />

          <ul className="mt-8 list-[circle]">
            <li>
              <h2 className="text-xl font-bold">
                Advantages of Proxy Server :{" "}
              </h2>

              <ol className="list-decimal mt-2 ml-8">
                <li>
                  <p className="mt-2 text-justify">
                  It hides the real identity of the user who accesses the Internet indirectly.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                  It is inexpensive compared to other hardware-based firewalls.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                  It does not require the use of valid IP addresses.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                  It is easy to configure and manage.
                  </p>
                </li>

                <li className="mt-2">
                  <p className="mt-2 text-justify">
                  It provides logging on the network.
                  </p>
                </li>
              </ol>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const PROXY_SERVER = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default PROXY_SERVER;
