import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import im_well from "../images/wellness.jpg";
import heal_active from "../images/healthy_ac.jpg";
import improves_health from "../images/improves_health.jpg";
import social_active from "../images/social_active.jpg"
import better_growth from "../images/better_growth.jpg"
import happy_life from "../images/happy_life.jpg"
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";


const related_blogs = [
  {
    url: "/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness",
    title: "What is Physical Fitness and Components of Physical Fitness",
  },

  {
    url: "/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness",
    title: "What is Physical Fitness and Components of Physical Fitness",
  },
];



const Content = () => {
  const meta = {
    title: "Importance of Wellness",
    description:
      "In this article you can read about Importance of Wellness. Healthy living means making choices to improve your health and minimize Controllable illnesses and diseases. Active living is doing physical activities in order to improve your fitness level.",
    canonical: "https://bangblogs.com/education-blogs/importance-of-wellness",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "wellness, importance of wellness",
      },
      property: {
        og_title: "Importance of Wellness",
        og_type: "article",
        og_url: "https://bangblogs.com/education-blogs/importance-of-wellness",
        og_description:
          "In this article you can read about Importance of Wellness. Healthy living means making choices to improve your health and minimize Controllable illnesses and diseases. Active living is doing physical activities in order to improve your fitness level.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Importance of Wellness
        </h1>
        <div className=" p-5">
            <img src={im_well} alt="importance of wellness" className="" />
            <ul className="list-[circle]">
                <li className="mt-4">
                    <h2 className="text-xl font-bold">Healthy Active Living : </h2>
                    <img src={heal_active} alt="healthy active living"  className="mt-2"/>
                    <p className="mt-2 text-justify">
                    Healthy living means making choices to improve your health and minimize Controllable illnesses and diseases. Active living is doing physical activities in order to improve your fitness level. The four dimensions of healthy active living are physical health, mental health, social health, and spiritual health.
                    </p>
                </li>
                <li className="mt-4">
                    <h2 className="text-xl font-bold">Improves Health : </h2>
                    <img src={improves_health} alt="Improves health"  className="mt-2"/>
                    <p className="mt-2 text-justify">
                        Wellness is important for efficient body functions which leads to good health and optimum development of the body. wellness makes the good personality of men.
                    </p>
                </li>

                <li className="mt-4">
                    <h2 className="text-xl font-bold">Become Active member of society : </h2>
                    <img src={social_active} alt="social active"  className="mt-2"/>
                    <p className="mt-2 text-justify">
                    Through physical fitness and wellness programs, we learn various social qualities which are very useful for better adjustment in society like discipline, cooperation, friendship, kindness, helpfulness, etc.
                    </p>
                </li>

                <li className="mt-4">
                    <h2 className="text-xl font-bold">Achieve Optimum Growth and Development : </h2>
                    <img src={better_growth} alt="better growth"  className="mt-2"/>
                    <p className="mt-2 text-justify">
                    Physical fitness and wellness help us to grow physically as well as mentally. It is important for enhancing the proper growth and development of an individual to enjoy a better life.
                    </p>
                </li>

                <li className="mt-4">
                    <h2 className="text-xl font-bold">Healthy and Happy Life : </h2>
                    <img src={happy_life} alt="happy life"  className="mt-2"/>
                    <p className="mt-2 text-justify">
                    Physical fitness and wellness programmes play an important role in improving the quality of one's life. In our daily life a fit person can manage daily routine work efficiently and without getting fatigued.
                    </p>
                </li>
            </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const IMPORTANCE_OF_WELLNESS = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default IMPORTANCE_OF_WELLNESS;
