import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import components_of_fitness from "../images/components_of_fitness.jpg";
import types_of_strength from "../images/types_of_strength.jpg";
import strength from "../images/strength.jpg";
import endurance from "../images/endurance.jpg";
import speed from "../images/speed.jpg";
import components_of_speedpeed from "../images/components_of_speed.jpg";
import flexibility from "../images/flexibility.jpg";
import coordination from "../images/coordination.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/education-blogs/what-is-adaptive-physical-education-and-aims-or-objectives-of-adaptive-physical-education",
    title: "What is Adaptive Physical Education and Aims or Objectives of Adaptive Physical Education",
  },

  {
    url: "/education-blogs/importance-of-healthy-lifestyle",
    title: "Importance of Healthy Lifestyle",
  },

  {
    url: "/education-blogs/leadership-and-qualities-of-leader",
    title: "Leadership and Qualities of Leader",
  }

];

const Content = () => {
  const meta = {
    title: "What is Physical Fitness and Components of Physical Fitness",
    description:
      "In this article you can read about Physical Fitness and Components of Physical Fitness.  Physical fitness is a strength of a person. With better physical fitness a person can perform various physical activities easily without any fatique and injury",
    canonical: "https://bangblogs.com/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Physical Fitness, Components of Physical Fitness",
      },
      property: {
        og_title: "What is Physical Fitness and Components of Physical Fitness",
        og_type: "article",
        og_url: "https://bangblogs.com/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness",
        og_description:
          "In this article you can read about  Importance of Healthy Lifestyle. Living a healthy lifestyle plays a vital role in improving our abilities.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Physical Fitness and Components of Physical Fitness
        </h1>
        <div className=" p-5">
          <img
            src={components_of_fitness}
            alt="components of physical fitness"
          />
          <p className="text-justify">
            Physical fitness is a strength of a person. With better physical
            fitness a person can perform various physical activities easily
            without any fatique and injury.
          </p>
          <p className="mt-2 text-justify">
            Physical fitness plays an important role in every life. with good
            Physical fitness, a person can perform various strength activities
            without any problem. Physical fitness has also some components which
            define the ability of a person in a particular section
          </p>
          <h2 className="mt-4 text-xl font-bold">
            Components of Physical Fitness
          </h2>
          <p className="mt-2 text-justify">
            There are various components of physical fitness. All athletes to
            know, what to develop and how to develop. There are five main
            physical fitness components and these are interrelated with each
            other directly or indirectly. Every component has its own importance
            in games, sports, and even in the daily life of the individual.
          </p>
          <ul>
            <li>
              <h3 className="mt-2 text-lg font-semibold mb-2">Strength : </h3>
              <img src={strength} alt="physical strength" />
              <p className="mt-2 text-justify">
                Strength is the ability of muscles to overcome resistance.
                Strength is necessary for the performance of physical
                activities, whether it is a physical activity associated with
                daily living. Strength can be defined as the amount of force a
                muscle can exert. Without proper strength, no activity can be
                performed, so it is a very important component of physical
                fitness. The level of strength may differ according to the
                nature of the activity, sex, age, etc. Different games require
                different levels and types of strength.
              </p>
              <img src={types_of_strength} alt="types of strength" />
              <ol className="ml-4 list-[decimal]">
                <li>
                  <h4 className="text-[15px] font-bold">Static Strength : </h4>
                  <p className="mt-2 text-justify">
                    Static strength is also called{" "}
                    <span className="font-bold">Isometric Strength</span>. It is
                    the ability of muscles to act against resistance in a static
                    position. In this, the length of the muscle does not change,
                    and there is no visible movement at the joint. Static
                    Strength can be measured by a dynamometer. Static strength
                    is used in weight lifting in phases (powerlifting), pushing
                    the wall, etc.
                  </p>
                </li>

                <li className="mt-2">
                  <h4 className="text-[15px] font-bold">Dynamic Strength : </h4>
                  <p className="mt-2 text-justify">
                    Dynamic strength is also called{" "}
                    <span className="font-bold">Isotonic strength</span>. It is
                    the ability of a person to do various movements or to
                    overcome resistance. While performing isotonic exercises the
                    movements of the body or muscles can be seen. In pull-ups
                    and push-ups, dynamic strength is required. Dynamic strength
                    has three parts :
                  </p>
                  <ol className="ml-4 mt-2 list-[lower-roman]">
                    <li>
                      <h4 className="text-[15px] font-bold">
                        Maximum Strength :{" "}
                      </h4>
                      <p className="mt-2 text-justify">
                        Maximum strength is the ability of muscles to act
                        against maximum resistance in a single effort. This type
                        of strength is used in weight-lifting power-wrestling,
                        jumping and throwing events, etc. Maximum strength is
                        not usually used in a majority of games and sports. In
                        some sports, maximum strength is required for a very
                        short period i.e. in gymnastics while performing at
                        Roman rings, starting and acceleration phase in sprints,
                        and during take off for a long jump, high jump, pole
                        vault, etc.
                      </p>
                    </li>

                    <li className="mt-2">
                      <h4 className="text-[15px] font-bold">
                        Explosive Strength :{" "}
                      </h4>
                      <p className="mt-2 text-justify">
                        Explosive strength can be explained as the ability to
                        overcome resistance with maximum or high speed. In
                        reality, this is the combination of strength and speed
                        ability of a player. The sports in which explosive
                        strength is required are the long jump, high jump,
                        triple jump, and crouch starting with sprinting, jumping
                        for a rebound in basketball, and jumping in volleyball
                        for smashing or spiking.
                      </p>
                    </li>

                    <li className="mt-2">
                      <h4 className="text-[15px] font-bold">
                        Strength Endurance :{" "}
                      </h4>
                      <p className="mt-2 text-justify">
                        It is the combination of endurance and strength. This is
                        the ability of an individual to overcome resistance
                        under fatigue conditions. Strength endurance is used in
                        swimming, long-distance races or road races, cycling,
                        pole vaulting, and in combative sports.
                      </p>
                    </li>
                  </ol>
                </li>
              </ol>
            </li>

            <li>
              <h3 className="mt-2 text-lg font-semibold mb-2">Endurance : </h3>
              <img src={endurance} alt="endurance" />
              <p className="mt-2 text-justify">
                Endurance is the ability to sustain an activity." "Endurance is
                the ability to do physical movements with desired quality and
                speed under conditions of fatigue." It is a very important
                component for games and sports i.e. middle and long-distance
                races, and games like hockey, basketball, kho-kho, football,
                etc.
              </p>
              <p className="mt-2 text-justify">
                <span className="font-bold">Cardiovascular endurance </span>is
                the ability of the heart, lungs, and blood vessels to provide
                oxygen even when the body is under the conditions of fatigue. It
                is required when the action is performed for a long duration and
                intensity is slow. It is required in long-distance races etc.
              </p>
            </li>

            <li className="mt-2">
              <h3 className="mt-2 text-lg font-semibold mb-2">Speed : </h3>
              <img src={speed} alt="speed" />
              <p className="mt-2 text-justify">
                It is the ability to perform the same pattern of movement at a
                faster rate. In other words, speed is the ability to move the
                body as fast as possible. The speed of an individual depends on
                heredity but it can be improved with the help of regular
                practice. Speed is needed in every game or activity. Speed is a
                very important component for sprinters and fast games. Speed can
                be divided into five parts:
              </p>
              <img
                src={components_of_speedpeed}
                alt="types of speed"
                className="mt-2 mb-2"
              />
              <ol className="ml-4 list-[decimal]">
                <li>
                  <h4 className="text-[15px] font-bold">Reaction Time : </h4>
                  <p className="mt-2 text-justify">
                    It is the time taken to perform an action as quickly as
                    possible to a signal. In other words, it enables a player to
                    react quickly and effectively to different types of
                    responses i.e., visual, auditory, etc.
                  </p>
                </li>

                <li className="mt-2">
                  <h4 className="text-[15px] font-bold">Movement Time : </h4>
                  <p className="mt-2 text-justify">
                    The speed of movement depends on how fast or quick action is
                    performed by the muscles. In other words, it is the ability
                    of muscles to do a single movement in minimum time. Speed of
                    movement can be measured through a reflex action test. Speed
                    training is developed by using high velocity for short
                    intervals.
                  </p>
                </li>

                <li className="mt-2">
                  <h4 className="text-[15px] font-bold"> Acceleration : </h4>
                  <p className="mt-2 text-justify">
                    It is the ability to gain maximum speed in minimum time.
                  </p>
                </li>

                <li className="mt-2">
                  <h4 className="text-[15px] font-bold">Speed Endurance : </h4>
                  <p className="mt-2 text-justify">
                    It is the ability to perform the movement at the fastest
                    speed under the condition of fatigue. It is the combination
                    of speed and endurance abilities.
                  </p>
                </li>

                <li className="mt-2">
                  <h4 className="text-[15px] font-bold">
                    Locomotor Ability :{" "}
                  </h4>
                  <p className="mt-2 text-justify">
                    It is the ability of an individual to maintain the speed for
                    maximum possible time or duration.
                  </p>
                </li>
              </ol>
            </li>

            <li>
              <h3 className="mt-2 text-lg font-semibold mb-2">
                Flexibility :{" "}
              </h3>
              <img src={flexibility} alt="flexibility" />
              <p className="mt-2 text-justify">
                Flexibility is the maximum range of movement possible at a joint
                the flexibility of the body varies significantly from joint to
                joint but depends upon the structure of joints, muscles,
                tendons, and ligaments. In order to maintain flexibility the
                body needs regular training (stretching). Stretching exercises
                can help to prevent injury. Flexibility can be measured with the
                help of a flexo-meter.
              </p>
            </li>

            <li>
              <h3 className="mt-2 text-lg font-semibold mb-2">
                Co-ordinative Abilities :{" "}
              </h3>
              <img src={coordination} alt="coordination" />
              <p className="mt-2 text-justify">
              Coordination ability means to do or perform a work activity efficiently with perfection. In other words, coordination ability is the working of the muscles and the brain together, without any unwanted movement. It can be improved with the help of regular training or exercise.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const PHYSICAL_FITNESS_COMPONENTS = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default PHYSICAL_FITNESS_COMPONENTS;
