import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page";

const MOBILE_PAGE = () => {

    const {mobile_page_blogs, s_text, mob_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={mobile_page_blogs} se_text={s_text.mobile_page_text} page_api={mob_api} dis_length={display_length.mb_p_b} page_blog_type="MOBILE_PAGE_BLOG" page_name="Mobile Blogs" seperate_page_for_search="MOBILE_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default MOBILE_PAGE;
