import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import computer_security from "../images/computer_security.jpg";
import computer_threats from "../images/computer_threats.jpg";
import computer_crime from "../images/computer_crime.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-proxy-server",
    title: "What is Proxy Server",
  },

  {
    url: "/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    title: " What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?",
  },

  {
    url: "/technology-blogs/what-is-malware-types-of-malware",
    title: "What is Malware and Types of Malware.",
  },
  
  {
    url: "/technology-blogs/what-is-hacker-and-classification-of-hackers",
    title: "What is Hacker and Classification of Hacker",
  }
];


const Content = () => {
  const meta = {
    title: "Computer Security, Computer Threats, Computer Crime",
    description:
      "In this article you can read about Computer Security, Computer Threats, Computer Crime.",
    canonical:
      "https://bangblogs.com/technology-blogs/computer-security-computer-threats-computer-crime",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Computer Security, Computer Threats, Computer Crime",
      },
      property: {
        og_title: "Computer Security, Computer Threats, Computer Crime",
        og_type: "article",
        og_url:
          "https://bangblogs.com/technology-blogs/computer-security-computer-threats-computer-crime",
        og_description:
          "In this article you can read about Computer Security, Computer Threats, Computer Crime.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Computer Security, Computer Threats, Computer Crime
        </h1>
        <div className=" p-5">
          <ul className="list-[circle]">
            <li className="mt-4">
              <h3 className="text-[16px] font-bold">Computer Security : </h3>
              <img
                src={computer_security}
                alt="Computer Security"
                className="mt-2 w-[200px]"
              />
              <p className="mt-2 text-justify">
                The security applied to computing devices such as computers and
                smartphones, as well as computer networks such as private and
                public networks, including the whole Internet, are called
                Computer Security. It includes physical security to prevent
                theft of equipment and information security to protect the data
                on that equipment. It is sometimes referred to as "Cyber
                Security" or "IT security". Cybersecurity is the process of
                applying security measures to ensure confidentiality, integrity,
                and availability of data.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="text-[16px] font-bold">Computer Threats : </h3>
              <img
                src={computer_threats}
                alt="Computer Threats"
                className="mt-2 w-[200px]"
              />
              <p className="mt-2 text-justify">
                A threat is a possible danger that might exploit a vulnerability
                to breach security and thus cause possible harm. It can be
                either "intentional" (i.e., intelligent; e.g., an individual
                cracker or a criminal organization) or "accidental" (e.g., the
                possibility of a computer malfunctioning, or the possibility of
                a natural disaster such as an earthquake, a fire, or a tornado)
                or otherwise a circumstance, capability, action, or event.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="text-[16px] font-bold">Computer Crime : </h3>
              <img
                src={computer_crime}
                alt="Computer Crime"
                className="mt-2 w-[200px]"
              />
              <p className="mt-2 text-justify">
                Computer crime refers to any crime that involves a computer and
                a network. Net crime refers to criminal exploitation of the
                Internet. Such crimes may threaten a nation’s security and
                financial health. Issues surrounding this type of crime have
                become high-profile, particularly those surrounding cracking,
                copyright infringement, child pornography, and child grooming.
                There are also problems of privacy when confidential information
                is lost or intercepted, lawfully or otherwise.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const COMPUTER_SECU = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default COMPUTER_SECU;
