import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import telnet from "../images/telnet.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-digital-subscriber-line-advantages-and-disadvantages",
    title: "What is Digital Subscriber Line, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    title: " What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?",
  }

];

const Content = () => {
  const meta = {
    title: "What is Telnet? Advantages of Telnet and Limitations of Telnet.",
    description:
      "In this article you can read about What is Telnet? Advantages of Telnet and Limitations of Telnet",
    canonical: "https://bangblogs.com/technology-blogs/telnet-advantages-and-limitations",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Telnet, Advantages of Telnet, Limitations of Telnet.",
      },
      property: {
        og_title: "What is Telnet? Advantages of Telnet and Limitations of Telnet.",
        og_type: "blog",
        og_url: "https://bangblogs.com/technology-blogs/telnet-advantages-and-limitations",
        og_description:
          "In this article you can read about What is Telnet? Advantages of Telnet and Limitations of Telnet",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Telnet? Advantages of Telnet and Limitations of Telnet.
        </h1>

        <div className=" p-5">
          <h2 className="text-xl font-bold mt-8">What is Telnet?</h2>
          <p className="mt-2 text-justify">
            Internet is a network of million of computers all around the world.
            It allows easy access to resources stored at remote computers
            somewhere else in the world from your own computer through a service
            known as Telnet.
          </p>

          <p className="mt-2 text-justify">
            Users can log in to their computers from another location.
            Similarly, consider that we have uploaded files on a FTP Server. If
            you want to perform some administration services on these files like
            setting (read/write) permissions then you can use the FTP client.
          </p>

          <h2 className="text-xl font-bold mt-8">How Telnet work?</h2>

          <p className="mt-2 text-justify">
            Telnet is a character-based communication protocol that provides a
            remote login capability. It is a part of the TCP/IP suite, the
            networking protocol of the Internet. It enables the user on a PC to
            log in to a remote computer and use the remote computer's resources
            with the illusion of working directly on the remote computer. The
            remote computer can be physically located in the same building, in
            different cities, or countries. Telnet also specifies. the standard
            of how the client encodes the keystrokes for transmission to the
            Server and how the server encodes the output for transmission to the
            client.
          </p>

          <p className="mt-2 text-justify">
            Telenet follows the client-server approach. The client's local
            computer first connects to the Internet and then contacts the Server
            with the Telnet program using the IP address/Name of the Server
            whose resources you want to use.
          </p>

          <img src={telnet} alt="telnet working" className="mt-4" />

          <h2 className="text-xl font-bold mt-8">Advantages of Telnet</h2>

          <ol className="list-decimal mt-2 ml-4">
            <li>
              <p className="mt-2 text-justify">
                You can access Information stored at the remote computer without
                actually being present there.
              </p>
            </li>

            <li className="mt-2">
              <p className="mt-2 text-justify">
                You can not only use telnet to access library catalogs or
                databases but can also use it to access Internet connection
                bulletin boards, play games with others (such as MUD (Multi-User
                Dungeon) games), and perform real-time services such as
                troubleshooting another person's computer without being present
                there.
              </p>
            </li>

            <li className="mt-2">
              <p className="mt-2 text-justify">
                It allows you to chat with any user connected using Telnet. Just
                type talk login where login is the actual login to the user you
                want to talk to. If the user accepts your chat request, a
                divided screen will appear and you will be able to chat.
              </p>
            </li>

            <li className="mt-2">
              <p className="mt-2 text-justify">
                It enables you to test your CGI scripts using the PICO editor
                without downloading them from the server.
              </p>
            </li>

            <li className="mt-2">
              <p className="mt-2 text-justify">
                It allows you to check your e-mails.
              </p>
            </li>

            <li className="mt-2">
              <p className="mt-2 text-justify">
                It also allows you to access web pages using the PICO editor.
              </p>
            </li>
          </ol>

          <h2 className="text-xl font-bold mt-8">Limitations of Telnet</h2>

          <ol className="list-decimal mt-2 ml-4">
            <li>
              <p className="mt-2 text-justify">
              you can gain access to a variety of information and services by using the Telnet but you cannot upload or download files. We use FTP to upload and download files.
              </p>
            </li>

            <li className="mt-2">
              <p className="mt-2 text-justify">
              Whenever you access the remote computer using telnet the information you type is sent to the remote computer as it is. So any intruder between you and the remote computer can read the information you type (such as passwords).
              </p>
            </li>

          </ol>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const TELNET = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default TELNET;
