import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import ip_address from "../images/ip_address.jpg";
import ip_cmd from "../images/ip_cmd.jpg";
import ip_in_google from "../images/ip_in_google.jpg";


const related_blogs = [
  {
    url: "/technology-blogs/what-is-domain-name-system-and-block-diagram",
    title: "What is Domain Name System (DNS)",
  },

  {
    url: "/technology-blogs/top-10-Intelligence-agencies-of-the-world",
    title: " Top 10 Intelligence agencies of the world",
  },

  {
    url: "/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    title: " What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?",
  }
];

const Content = () => {
  const meta = {
    title: "What is IP Address | types of IP Address | How to Find the IP Address of a Computer",
    description:
      "In this article you can read about IP Address, types of IP Address and How to Find the IP Address of a Computer.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-ip-address-and-types-how-to-find-my-ip-address",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "IP Address, types of IP Address, Find the IP Address of a Computer",
      },
      property: {
        og_title: "What is IP Address | types of IP Address | How to Find the IP Address of a Computer",
        og_type: "article",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-ip-address-and-types-how-to-find-my-ip-address",
        og_description:
          "In this article you can read about IP Address, types of IP Address and How to Find the IP Address of a Computer.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is IP Address and types of IP Address.
        </h1>

        <div className=" p-5">
            <img src={ip_address} alt="ip address" className="mt-2 mb-2" />
          <p className="mt-2 text-justify">
            An Internet Protocol address (IP address) is a numerical label
            assigned to each device (e.g., computer, printer) participating in a
            computer network that uses the Internet Protocol for communication.
            An IP address serves two principal functions: host or network
            interface identification and location addressing. Its role has been
            characterized as follows: "A name indicates what we seek. An address
            indicates where it is. A route indicates how to get there." IP
            addresses are binary numbers, but they are usually stored in text
            files and displayed in human-readable notations, such as
            172.16.254.1 (for IPv4), and 2001:db8:0:1234:0:567:8:1 (for IPv6).
          </p>

          <h2 className="text-xl font-bold mt-8">Types of IP Address</h2>
          <ul className="list-[circle]">
            <li className="mt-4">
              <h3 className="font-bold">Private IP Address : </h3>
              <p className="mt-2 text-justify">
                A private IP address is one that is assigned to a computer in
                the Local Area Network (LAN). A typical example of a private IP
                address would be something like: 192.168.0.4
              </p>
            </li>

            <li className="mt-4">
              <h3 className="font-bold">Public IP Address : </h3>
              <p className="mt-2 text-justify">
                A public IP address is one that is assigned to a computer
                connected to the Internet. An example public IP address would be
                something like: 59.93.115.119 In most cases, a computer gets
                connected to the ISP network using a private IP. Once a computer
                is on the ISP network it will be assigned a public IP address
                using which the communication with the Internet is made
                possible.
              </p>
            </li>
          </ul>

          <h2 className="text-2xl mt-8 font-bold">
            How to Find the IP Address of a Computer?
          </h2>
          <p className="mt-4 text-justify">
            Finding a mac address is extremely simple. just open a browser and
            type "what is my IP", you can easily see your Public IP Address.
          </p>

          <img src={ip_in_google} alt="public IP address in google" className="mt-2 mb-2" />

          <p className="mt-4 text-justify">
            In order to find your private IP, just open the command prompt
            window (type cmd in the “Run” box) and enter the following command:
          </p>

          <img src={ip_cmd} alt="private IP address in CMD" className="mt-2 mb-2"/>


          <p className="mt-4 text-justify">
            This will display a long list of details about your computer’s
            network devices and their configuration. To see your private IP
            address, just scroll down to find “IPv4 Address” which is nothing
            but your private IP.
          </p>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const IP_ADDRESS = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default IP_ADDRESS;
