import React, {useState} from 'react';
import { useNavigate } from "react-router-dom";
import axios from 'axios';

const Contact = () => {

  const navigate = useNavigate();

  const [user, setUser] = useState({
    name: "",
    email: "",
});

let nameOfFi, value;

const trackvalue = (e) => {
  nameOfFi = e.target.name;
  value = e.target.value;

  setUser(values => ({ ...values, [nameOfFi]: value }));
}

const validation = (v) => {

  v.preventDefault();

  const { name, email, } = user;

  try {
     axios.post("http://localhost:5000/contact", {
          name, email,
      }).then(() => {
          window.alert("contact succesfully");
          navigate("/home");
      }).catch((err) => {
          if (err.response.status === 422 || err.response.status === "") {
              window.alert("Invalid credentials");
          }
      });

  } catch (err) {

      console.log(err);
  }


}


  return (
    <section>
        <div className='main-contact-con'>
            <h1>Contact us</h1>
            <div className='contact-form-con'>
            <form method='POST' className="" onSubmit={validation}>
                <input type="text" placeholder='Name' name='name' onChange={trackvalue} value={user.name || ""}/>
                <input type="text" placeholder='Email address' name='email' onChange={trackvalue} value={user.email || ""}/>
                <textarea name="" id="" cols="10" rows="4"></textarea>
            </form>
            <div className='contact-cir'>
                <div></div>
                <div></div>
            </div>
            </div>
        </div>
    </section>
  )
}

export default Contact