import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import inclusive from "../images/inclusive.jpg";

const related_blogs = [
  {
    url: "/education-blogs/what-is-adaptive-physical-education-and-aims-or-objectives-of-adaptive-physical-education",
    title: "What is Adaptive Physical Education and Aims or Objectives of Adaptive Physical Education",
  },

  {
    url: "/education-blogs/leadership-and-qualities-of-leader",
    title: "Leadership and Qualities of Leader",
  },
];


const Content = () => {
  const meta = {
    title:
      "What is Inclusion, Its Need, and Implementation",
    description:
      "In this article you can read about Inclusion, Its Need, and Implementation. Inclusive Education is a new approach to educating children with disability and learning difficulties with that normal one within the same roof.",
    canonical:
      "https://bangblogs.com/education-blogs/what-is-inclusion-its-need-and-implementation",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Adaptive Physical, Objectives of Adaptive Physical Education, Aims of Adaptive Physical Education",
      },
      property: {
        og_title:
          "What is Inclusion, Its Need, and Implementation",
        og_type: "article",
        og_url:
          "https://bangblogs.com/education-blogs/what-is-inclusion-its-need-and-implementation",
        og_description:
          "In this article you can read about Inclusion, Its Need, and Implementation. Inclusive Education is a new approach to educating children with disability and learning difficulties with that normal one within the same roof.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Inclusion, Its Need, and Implementation
        </h1>
        <div className=" p-5">
          <img src={inclusive} alt="inclusive" className="h-[300px] w-[100%] object-contain"/>
          <p className="text-justify mt-2">
            Inclusive Education is a new approach to educating children with
            disability and learning difficulties with that normal one within the
            same roof. It brings all students together in one classroom and
            community, regardless of their strengths or weakness in any area.
          </p>

          <p className="text-justify mt-2">
            It is one of the most effective ways in which to promote an
            inclusive and tolerant society. About eighty percent of India's
            population lives in rural areas without provision for special
            schools. It means, there are an estimated 8 million children out of
            school in India. the central and state government have to manage
            inclusive classrooms.
          </p>

          <h2 className="text-xl font-bold mt-4">
            Need and Importance of Inclusive Education
          </h2>

          <ul className="list-[circle]">
            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                To provide equal oppotunities :{" "}
              </h3>
              <p className="text-justify">
                There have been efforts internationally to include children with
                disabilities in the educational mainstream. In order to achieve
                truly inclusive education, we need to think about and
                incorporate children with special needs into regular schools.
                Especially, because these kids face some sort of barrier to
                learning and participation in the classroom. Thus inclusive
                education is needed to provide these children with special needs
                equal opportunities as given to any other child.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                To promote uniqueness of each student :{" "}
              </h3>
              <p className="text-justify">
                General education classrooms include more and more diverse
                students, teachers should realize the value of accepting each
                student as unique. Ineffective inclusive programs, teachers
                adapt activities to include all students, even though their
                individual goals may be different. We have learned that
                inclusive education is a better way to help all students
                succeed.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                To promote better academic achievement :{" "}
              </h3>
              <p className="text-justify">
                Most students learn and perform better when exposed to the
                richness of the general education curriculum. The growing body
                of research has shown that children do better academically when
                in inclusive settings and Inclusion provides opportunities to
                develop relationships. Some of the benefits include friendships,
                social skills, personal principles, comfort level with people
                who have special needs, and caring classroom environments. The
                most important function of friendships is to make people feel
                cared for, loved, and safe.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                To develop social skills :{" "}
              </h3>
              <p className="text-justify">
                An interesting side effect is that these parents report that
                they also feel more comfortable with people with special needs
                because of their child's experiences. A student with
                disabilities can create long-lasting friendships that would not
                be otherwise possible, and these friendships can give them
                skills to navigate social relationships later on in life.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const INCLUSION_IMPLEMENTATION = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default INCLUSION_IMPLEMENTATION;
