import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import cellular_network from "../images/cellular_network.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/telnet-advantages-and-limitations",
    title: "What is Telnet? Advantages of Telnet and Limitations of Telnet.",
  },

  {
    url: "/technology-blogs/what-is-ftp-file-transfer-protocol-and-how-ftp-works",
    title: " What is FTP (File Transfer Protocol). Advantages, Disadvantages and How FTP works ?",
  },

  {
    url: "/technology-blogs/mobile-telephony-and-evolution-of-mobile-telephony",
    title: "Mobile telephony and Evolution of Mobile telophony",
  }
];

const Content = () => {
  const meta = {
    title: "What is Cellular Network? Working of Cellular Telephony",
    description:
      "In this Blog you can read aboutWhat is Cellular Network? Working of Cellular Telephony.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-cellular-network-and-woking-of-cellular-telephony",
    meta: {
      charset: "utf-8",
      name: {
        keywords: " Cellular Network, Working of Cellular Telephony",
      },
      property: {
        og_title: "What is Cellular Network? Working of Cellular Telephony",
        og_type: "blog",
        og_url:
          "https://bangblogs.com/technology-blogs/what-is-cellular-network-and-woking-of-cellular-telephony",
        og_description:
          "In this Blog you can read aboutWhat is Cellular Network? Working of Cellular Telephony.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Cellular Network? Working of Cellular Telephony.
        </h1>

        <div className=" p-5">
          <h2 className="text-xl font-bold mt-8 mb-4">Cellular Network?</h2>
          <p className="text-justify">
            To make this tracking possible, each cellular service area is
            divided into small regions called cells whose size may vary from 1
            to 12 miles each cell contains an antenna and is controlled by a
            solar or AC-powered network station called a base station which in
            turn is followed by a switching office, called a Mobile Switching
            Center (MSC), is a computerized center that is responsible for
            connecting calls, recordings, call information and billing.
          </p>

          <p className="text-justify mt-4">
            The size of each cell depends on three factor.
          </p>

          <ol className="list-decimal ml-4 mt-4 font-extrabold text-sm">
            <li className="mt-4">
              <p className="font-light text-[16px]">
                First, the local terrain radio signals are blocked by trees,
                hills, and buildings.
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
                Second, the frequency band in which the network operates (in
                general, the higher the radio frequency, the smaller the cell).
              </p>
            </li>

            <li className="mt-4">
              <p className="font-light text-[16px]">
                Third, the capacity needed in any given area. Base stations are
                typically spaced about 0.2-0.5 km in towns and 2-5 km apart in
                the countryside.
              </p>
            </li>
          </ol>

          <p className="text-justify mt-4">
            In cellular telephony, a service provider must be able to track a
            caller, locate it, assign a channel to the call, and transfer the
            channel from one Base Station (BS) to another base station as the
            caller moves out of range. Base stations are connected to one
            another by central switching centers.
          </p>

          <p className="text-justify mt-4">
            The transmission power of each cell is kept lower to prevent its
            signal from interference with those of other cells, but the set of
            frequencies available is limited and frequencies need to be reused.
            A frequency reuse pattern is a configuration of n cells, n being the
            reuse factor, in which each cell uses a unique set of frequencies,
            and when the pattern is repeated, and the frequencies can be reused.
          </p>

          <img src={cellular_network} alt="cellular network" />

          <h2 className="text-xl font-bold mt-8 mb-4">
            Working of Cellular Telephony
          </h2>

          <ol className="list-decimal ml-4 mt-4 font-extrabold text-lg">
            <li className="mt-4">
              <h3>Call initialtion : </h3>
              <p className="font-light text-[16px] mt-2">
                To place a call from a mobile/mobile station, the caller enters
                a code of 7 or 10 digits (a phone number) and pressed the
                calling button.
              </p>

              <p className="font-light text-[16px] mt-2">
                The mobile station then scans the band and sends the phone
                number to the closest BS. BS relays data to the MSC, which sense
                the data to the telephone central office. If the called party is
                available connection is made and the result is relayed back to
                MSC. MSC assigns an unused voice channel to the call, the
                connection is then established and MS automatically adjusts its
                tuning to the new channel and Communication goes on.
              </p>
            </li>

            <li className="mt-4">
              <h3>Receiving a Call : </h3>
              <p className="font-light text-[16px] mt-2">
              As soon as a mobile phone is called, the telephone central office sends the number to MSC. Now MSC searches for the location of an MS. by sending a query signal to each cell in a process called paging.
              </p>

              <p className="font-light text-[16px] mt-2">
              One MS is found, MSC transmits a ringing signal and when MS answers, it assigns a voice channel to the call, allowing voice communication to begin.
              </p>
            </li>

            <li className="mt-4">
              <h3>Handoff : </h3>
              <p className="font-light text-[16px] mt-2">
              It may happen during a conversation, MS moves from one cell to another due to which the signal may become weak. To solve this problem MSC monitors the level of the signal every few seconds and if the strength of the signal diminished MSC seeks a new cell that can provide better communication (hands the signal off from the old channel to a new one).
              </p>

              <p className="font-light text-[16px] mt-2">
              Early systems were using a hard handoff in which MS only communicates with one BS and if MS moves from one cell to another, at first previous BS is broken then a new connection can be established (bad communication).
              </p>
            </li>

            <li className="mt-4">
              <h3>Roaming : </h3>
              <p className="font-light text-[16px] mt-2">
              One of the most striking features of cellular communication is roaming, which means a user can have access to communication where there is coverage. As service providers usually have limited coverage so neighboring service providers can provide extended coverage through a roaming contract.
              </p>
            </li>

            <li className="mt-4">
              <h3>Frequency reuse : </h3>
              <p className="font-light text-[16px] mt-2">
              For cellular networks, the limited range of high-frequency waves is advantageous because it means the same frequencies can be reused at nearby locations. Cell phone calls connect with short-range antennas known as towers. If there were only one tower for a large area, more customers would be trying to use the same high-frequency waves, and these waves would tend to overlap and cause interference.
              </p>
            </li>
          </ol>
        </div>

        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const CELLULAR_NETWORK = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default CELLULAR_NETWORK;
