import { useGloabalContext } from "../pages/ContextAPI";


const Pagination = ({ display_pages, total_pages, specific_blog }) => {
  
  const { preBlogs, nextBlogs} = useGloabalContext();

    return(
    <div className="main-blog-navigation">
      <button className="bg-gray-100" onClick={(e)=>preBlogs(e.target.name)} name={specific_blog}>Previous</button>
      <p>{display_pages} of {total_pages}</p>
      <button className="bg-gray-100" type="button" name={specific_blog} onClick={(e) => { nextBlogs(e.target.name);}}>
        Next
      </button>
    </div>)
  };

export default Pagination;