import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import scr from "../images/SCR.jpg";
import scr_parts from "../images/scr_part.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-digital-subscriber-line-advantages-and-disadvantages",
    title: "What is Digital Subscriber Line, Advantages and Disadvantages",
  },

];


const Content = () => {
  const meta = {
    title: " What is Silicon Controlled Rectifier (SCR) | Voltage rating",
    description: "In this article you can read about Silicon Controlled Rectifier (SCR) and its voltage. What is Silicon Controlled Rectifier (SCR) and Voltage rating",
    canonical: "https://bangblogs.com/education-blogs/what-is-silicon-controlled-rectifier-SRC-and-voltage-rating",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Silicon Controlled Rectifier, SCR, Voltage rating of Silicon Controlled Rectifier, SCR in digital electronics",
      },
      property : {
        og_title: "Silicon Controlled Rectifier (SCR) | Voltage rating",
        og_type : "article",
        og_url : "https://bangblogs.com/education-blogs/what-is-silicon-controlled-rectifier-SRC-and-voltage-rating",
        og_description : "In this article you can read about Silicon Controlled Rectifier (SCR) and its voltage. What is Silicon Controlled Rectifier (SCR) and Voltage rating",
        og_site_name : "bangblogs"
      }
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Silicon Controlled Rectifier (SCR) and Voltage rating
        </h1>
        <div className=" p-5">
          <img src={scr} alt="Silicon Controlled Rectifier SCR" />
          <ul className="list-[square]">
            <li>
              <h2 className="text-xl font-bold">
                Silicon Controlled Rectifier (SCR)
              </h2>
              <p className="mt-4 text-justify">
                The Silicon Controlled Rectifier, usually refferred to as an
                SCR, is one of the family of semiconductors that includes
                transistors and diodes. Although it is not the same as either a
                diode or a transistor, the SCR combines features of both.
                Circuits using transistors or rectifier diodes may be greatly
                improved in some instances through the use of SCRs.
              </p>

              <p className="mt-4 text-justify">
                SCRs are manufactured from silicon, these are not manufactured
                from germanium which rectifies the signal in a controlled
                manner. The current, voltage and thermal handling capabilities
                of silicon are much better than germanium.
              </p>

              <p className="mt-4 text-justify">
                SCR is basically a thyristor. The basic purpose of the SCR is to
                function as switch that can turn on or off or large amount of
                power. It performs this function with no moving parts that wear
                out and no points that require replacing.
              </p>

              <img
                src={scr_parts}
                alt="parts of silicon controlled rectifier"
              />

              <p className="  text-justify">
                Silicon Controlled Rectifiers are available in a variety of
                Integrated Circuit (IC) packages and with different number of
                pins. Basic IC package type include:
              </p>

              <ol className="list-[lower-roman] ml-5 mt-3">
                  <li>Dual In-line Package (DIP)</li>
                  <li>Diode Outline (DO)</li>
                  <li>Transitor Outline (TO)</li>
                  <li>Small Outline Diode (SOD)</li>
                  <li>Small Outline Transistor (SOT)</li>
                </ol>

              <p className="mt-4 text-justify">
                SCRs are made with voltage ratings of upto to 7500 volts, and
                with current ratings up to 3000 rms amperes per device. Some of
                the larger ones can take 50 kA in single-pulse operation. SCRs
                are used in power switching, phase control, chopper, battery
                chargers, and inverter circuits.
              </p>
            </li>
            <li className="mt-4">
              <h2 className="text-xl font-bold">Voltage Ratings</h2>
              <ol className="list-[lower-roman] ml-5 mr-5 mt-3">
                <li className="">
                  <h3 className="uppercase font-bold">
                    Critical Rate of Rise OFF-STATE Voltage
                    <span className="lowercase"> (dV/dt) Or </span> Breakover :
                  </h3>
                  <p className="mt-2 text-justify">
                    It is also, commonly calles static dv/dt. It is the minimum
                    value of the rate of rise of forward voltage which cause
                    switching from OFF-state to ON-state with gate open.
                  </p>
                </li>

                <li className="mt-3">
                  <h3 className="uppercase font-bold">
                    peak repetitive off-state forward voltage (v<sub>gd</sub>) :
                  </h3>
                  <p className="mt-2 text-justify">
                    At the maximum peak value of voltage allowed between the
                    gate and cathode terminals OFF-state voltage applied, this
                    parameter specifies the maximum DC voltage that can be
                    applied to the gate and still not switch the device from
                    OFF-state to ON-state.
                  </p>
                </li>

                <li className="mt-3">
                  <h3 className="uppercase font-bold">
                    forward peak gate voltage (v<sub>gm</sub>) :
                  </h3>
                  <p className="mt-2 text-justify">
                    The maximum peak value of voltage allowed vetween the gate
                    and cathode terminals with these terminals forward biased
                    fon an SCR.
                  </p>
                </li>

                <li className="mt-3">
                  <h3 className="uppercase font-bold">
                    gate trigger voltage (v<sub>gt</sub>) :
                  </h3>
                  <p className="mt-2 text-justify">
                    The gate DC voltage required to produce the gate trigger
                    current.
                  </p>
                </li>

                <li className="mt-3">
                  <h3 className="uppercase font-bold">
                    rms isolation voltage (v<sub>Iso</sub>) :
                  </h3>
                  <p className="mt-2 text-justify">
                    It is the dielectric withstanding voltage capability of a
                    SCR between the active portion of the device and the heat
                    sink. Relative humidity is a specified condition.
                  </p>
                </li>

                <li className="mt-3">
                  <h3 className="uppercase font-bold">
                    peak reverse gate blocking voltage (v<sub>rrm</sub>) :
                  </h3>
                  <p className="mt-2 text-justify">
                    It is the maximum allowable peak reverse voltage applied to
                    the gate on an SCR, measured at a specified I<sub>GR</sub>{" "}
                    which is the reverse gate current.
                  </p>
                </li>

                <li className="mt-3">
                  <h3 className="uppercase font-bold">
                    peak forward on state voltage (v<sub>tm</sub>) :
                  </h3>
                  <p className="mt-2 text-justify">
                    It is the maximum voltage drop access the main terminals at
                    stated conditions when the devices are in the ON-state. To
                    prevent heating of the junction, the IV<sub>TM</sub> is
                    measured at a short pulse width and low duty cycle.
                  </p>
                </li>

                <li className="mt-3">
                  <h3 className="uppercase font-bold">
                    peak reverse voltage (prv) :{" "}
                  </h3>
                  <p className="mt-2 text-justify">
                    This is a important parameter while considering applications
                    of SCR in ac circuits.
                  </p>
                </li>
              </ol>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const SCR_VOLTAGE = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default SCR_VOLTAGE;
