import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import rectifier from "../images/rectifier.jpg";
import types_rec from "../images/types_rectifiers.jpg";
import sub_types_rec from "../images/sub_types_rectifiers.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";

const related_blogs = [
  {
    url: "/education-blogs/what-is-silicon-controlled-rectifier-SRC-and-voltage-rating",
    title: "What is Silicon Controlled Rectifier (SCR) and Voltage rating",
  },

  {
    url: "/education-blogs/working-principal-of-a-single-phase-full-wave-half-controlled-rectifier",
    title:"Working principle of a single-phase full wave half controlled rectifier",
  },


];

const Content = () => {
  const meta = {
    title: " What are Rectifiers | types of Rectifiers",
    description:
      "In this article you can read about Rectifiers and its types. Rectifiers are the circuits used for converting AC to DC power by using power diodes or by controlling the firing angles of thyristors/controllable switches",
    canonical:
      "https://bangblogs.com/education-blogs/what-are-rectifiers-its-application-and-types",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "rectifiers, application of rectifiers, types of rectifiers, semi-controled rectifier",
      },
      property: {
        og_title: "What are Rectifiers | types of Rectifiers",
        og_type: "article",
        og_url:
          "https://bangblogs.com/education-blogs/what-are-rectifiers-its-application-and-types",
        og_description:
          "In this article you can read about Rectifiers and its types. Rectifiers are the circuits used for converting AC to DC power by using power diodes or by controlling the firing angles of thyristors/controllable switches",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What are Rectifiers and its types
        </h1>
        <div className=" p-5">
          <ul className="list-[square]">
            <li>
              <h2 className="text-xl font-bold">Rectifiers</h2>
              <img src={rectifier} alt="rectifier block diagram" />
              <p className="mt-4 text-justify">
                Rectifiers are the circuits used for converting AC to DC power
                by using power diodes or by controlling the firing angles of
                thyristors/controllable switches. The thyristors need to be
                turned on using a special triggering circuit.
              </p>

              <p className="mt-4 text-justify">
                The input supply may be a single-phase or a multi-phase supply.
                The output is DC voltage and current with a certain amount of
                ripple components.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">Application of Rectifiers</h2>
              <ol className="mt-4 list-[lower-roman]">
                <li>DC welder</li>
                <li>DC motor drive</li>
                <li>Battery charger</li>
                <li>DC power supply</li>
                <li>HVDC ( High-voltage direct current )</li>
                <li>AC ( Alternating current )</li>
              </ol>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">Types of Rectifiers</h2>
              <img src={types_rec} alt="rectifier types" />
              <ol className="mt-4 list-[lower-roman] font-bold">
                <li>
                  <h3 className="text-[1rem]">Half-wave Rectifier</h3>
                  <p className="mt-2 font-light text-justify">
                    The half-wave, a rectifier is used most often in low-power
                    applications because the average current in the supply will
                    not be zero. This may cause problems in transformer
                    performance. while practical applications of half-wave
                    rectifiers are limited, the analysis is crucial because it
                    will enable us to understand more complicated circuits such
                    as full-wave and three-phase rectifiers.
                  </p>
                </li>

                <li>
                  <h3 className="mt-2 text-[1rem]">Full-wave Rectifier</h3>
                  <p className="mt-2 font-light text-justify">
                    Like half-wave, the objective of a full-wave rectifier is to
                    produce a voltage or current which is purely DC or has some
                    specified DC component. While the purpose of the full-wave
                    rectifier is the same as that of the half-wave rectifiers,
                    it has some fundamental advantages. The average current in
                    the ac source is zero in the full-wave rectifier, thus
                    avoiding problems associated with non-zero moderate source
                    currents. The average (DC) output voltage is higher than
                    half-wave. The output of the full-wave is inherently less
                    ripple than that in the half-wave rectifier.
                  </p>
                  <h3 className="mt-2 text-[1rem]">FURTHER SUBDIVISION : </h3>
                  <p className="mt-2 font-light text-justify">
                    Each type can either be uncontrolled, half-controlled, or
                    fully controlled. An uncontrolled rectifier uses diodes,
                    while a full-controlled rectifier uses a thyristor or
                    popularly known as Silicon Controlled Rectifier (SCR). A
                    half controlled is a mix of diodes and thyristors.
                  </p>
                  <img src={sub_types_rec} alt="types of full-wave rectifiers" />
                  <ol className="mt-4 ml-5 list-[decimal] font-bold">
                    <li>
                      <h3 className="mt-2 text-[1rem]">
                        Uncontrolled Rectifiers :
                      </h3>
                      <p className="mt-2 font-light text-justify">
                        Single-phase uncontrolled rectifiers are extensively
                        used in a number of power electronic-based converters.
                        When all the switching elements of the circuit are
                        semiconductor diodes, the circuits are termed
                        uncontrolled rectifiers. Being a normal diode they don't
                        have the means to control the output signal. Each diode
                        automatically turns on when it is forward-biased and
                        turns off when it becomes reverse-biased.
                      </p>

                      <p className="mt-2 font-light text-justify">
                        These rectifiers provide a fixed DC output voltage for a
                        given AC supply.
                      </p>

                      <p className="mt-2 font-light text-justify">
                      In most cases, they are used to provide an intermediate unregulated DC voltage source which is further processed to obtain a regulated DC or AC output. They have, in general, been proved to be efficient and robust power stages.
                      </p>

                    </li>

                    <li>
                      <h3 className="mt-2 text-[1rem]">
                        Controlled Rectifiers :
                      </h3>
                      <p className="mt-2 font-light text-justify">
                      When all the switching elements of the circuit are thyristors, the circuits are termed controlled rectifiers. These rectifiers provide controlled rectified output.
                      </p>

                      <p className="mt-2 font-light text-justify">
                      The controlled rectifiers provide an adjustable DC output voltage by controlling the phase at which the devices are turned ON. and it allows to flow the power on both sides.
                      </p>

                    </li>

                    <li>
                      <h3 className="mt-2 text-[1rem]">
                        Semi-ontrolled Rectifiers :
                      </h3>
                      <p className="mt-2 font-light text-justify">
                      This is the third category of rectifiers called Semi-controlled Rectifiers. It is also known as half-controlled rectifiers. As the name itself indicates, these rectifiers are half-controlled. In these circuits, some of the switching devices are thyristors and the rest are diodes.
                      </p>

                    </li>
                  </ol>
                </li>
              </ol>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const RECTIFIERS_TYPES = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default RECTIFIERS_TYPES;
