import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import network_address from "../images/network_address.jpg";

const related_blogs = [
  {
    url: "/technology-blogs/what-is-wireless-internet-connection-advantages-and-disadvantages",
    title: "What is Wireless Internet Connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/what-is-dial-up-advantages-and-disadvantages",
    title: "What is Dial-up connection, Advantages and Disadvantages",
  },

  {
    url: "/technology-blogs/who-control-the-internet",
    title: "Who Controls the Internet",
  }

];

const Content = () => {
  const meta = {
    title:
      "What is network address class and various types of network classes",
    description:
      "In this article you can read about network address class and various types of network classes. The Network Address classes are currently divided into five classes: Class A, Class B, Class C, Class D, and Class E. Classes A, B, and C are generally used and classes D and E are reserved for particular use only. These classes define which bits are used for Netid and which for HostId.",
    canonical:
      "https://bangblogs.com/technology-blogs/what-is-network-address-class-and-various-types-of-network-classes",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "network address class, types of network classes",
      },
      property: {
        og_title:
          "What is network address class and various types of network classes",
        og_type: "article",
        og_url:
          "https://bangblogs.com/technology-blogswhat-is-network-address-class-and-various-types-of-network-classes",
        og_description:
          "In this article you can read about network address class and various types of network classes. The Network Address classes are currently divided into five classes: Class A, Class B, Class C, Class D, and Class E. Classes A, B, and C are generally used and classes D and E are reserved for particular use only. These classes define which bits are used for Netid and which for HostId.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is network address class and various types of network classes
        </h1>

        <div className=" p-5">
            <img src={network_address} alt="network address classes" />
          <p className="mt-2 text-justify">
            The Network Address classes are currently divided into five classes:
            Class A, Class B, Class C, Class D, and Class E. Classes A, B, and C
            are generally used and classes D and E are reserved for particular
            use only. These classes define which bits are used for Netid and
            which for HostId. These classes also describe the possible number of
            networks and number of hosts in each network.
          </p>

          <h2 className="text-2xl font-bold mt-4">
            First three Network Address Classes
          </h2>

          <table className="border-collapse border border-slate-400 box-border mt-2">
            <tr>
              <th className="border border-slate-300 p-2">Class</th>
              <th className="border border-slate-300 p-2">
                Highest order bits
              </th>
              <th className="border border-slate-300 p-2">
                First Octect Decimal Range
              </th>
              <th className="border border-slate-300 p-2">
                Maximum Networks available
              </th>
              <th className="border border-slate-300 p-2">
                Maximun Hosts available per Network
              </th>
              <th className="border border-slate-300 p-2">IP address</th>
            </tr>
            <tr>
              <td className="border border-slate-300 p-2">A</td>
              <td className="border border-slate-300 p-2">0</td>
              <td className="border border-slate-300 p-2">1 to 126</td>
              <td className="border border-slate-300 p-2">
                126(2<sup>7</sup>-2)
              </td>
              <td className="border border-slate-300 p-2">
                16777214 (2<sup>24</sup>-2)
              </td>
              <td className="border border-slate-300 p-2 flex flex-col text-[10px]">
                <span className="border border-red-300">
                  <span className="border border-red-300"> W </span>. X . Y . Z
                </span>
                <span>119 . 92 . 74 . 6</span>
              </td>
            </tr>
            <tr>
              <td className="border border-slate-300 p-2">B</td>
              <td className="border border-slate-300 p-2">10</td>
              <td className="border border-slate-300 p-2">128 to 191</td>
              <td className="border border-slate-300 p-2">
                16384(2<sup>14</sup>)
              </td>
              <td className="border border-slate-300 p-2">
                65534 (2<sup>16</sup>-2)
              </td>
              <td className="border border-slate-300 p-2 flex flex-col text-[10px]">
                <span className="border border-red-300">
                  <span className="border border-red-300"> W . X </span>. Y . Z
                </span>
                <span>172 . 34 . 72 . 8</span>
              </td>
            </tr>
            <tr>
              <td className="border border-slate-300 p-2">C</td>
              <td className="border border-slate-300 p-2">110</td>
              <td className="border border-slate-300 p-2">192 to 223</td>
              <td className="border border-slate-300 p-2">
                2097152(2<sup>21</sup>)
              </td>
              <td className="border border-slate-300 p-2">
                254 (2<sup>8</sup>-2)
              </td>
              <td className="border border-slate-300 p-2 flex flex-col text-[10px]">
                <span className="border border-red-300">
                  <span className="border border-red-300"> W . X . Y </span>. Z
                </span>
                <span>194 . 57 . 76 . 29</span>
              </td>
            </tr>
          </table>

          <h2 className="text-xl font-bold mt-8">
            Various Types Network Classes
          </h2>
          <ul className="list-[circle]">
            <li className="mt-4">
              <h3 className="font-bold">Class A addresses : </h3>
              <p className="mt-2 text-justify">
                The Class A addresses are normally used by those organizations
                that need to provide access to a very large number of hosts. The
                highest order bit of class A is always set to 0 and the
                remaining 7 bits represent the Network Id (NetId). The rest of
                the 24 bits are used to represent the HostId. The maximum number
                of NetId's and HostId's possible in class A are 126 (2
                <sup>7</sup>-2) and 16777214 (2<sup>24</sup>-2) respectively.
                This class only uses the first octect to designate the NetId
              </p>
            </li>

            <li className="mt-4">
              <h3 className="font-bold">Class B addresses : </h3>
              <p className="mt-2 text-justify">
                The Class B addresses are normally used by those organizations
                that are medium-sized. The highest order bit of class B is
                always set to 10 and the remaining 14 bits represent the NetId.
                The rest of the 16 bits are used to represent the HostId. The
                maximum number of Networks and Host that can be connected to a
                network possible in class B is 16384 (2<sup>14</sup>) and 65534
                (2<sup>16</sup>-2) respectively. The class B uses first and
                second octect to designate the NetId.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="font-bold">Class C addresses : </h3>
              <p className="mt-2 text-justify">
                The Class C addresses are normally used by those organizations
                that have small networks. The highest order bit of Class C is
                always set to 110 and the remaining 21 bits represent the Netld.
                The rest of the 8 bits are used to represent the Hostld. The
                maximum number of Networks and Hosts that can be connected to a
                network possible in Class C are 2097152 (2<sup>21</sup>) and 254
                (2<sup>8</sup>-2) respectively. The Class C was first three
                octects to designate the Netid. Now a days, Class C is used
                commonly.
              </p>
            </li>

            <li className="mt-4">
              <h3 className="font-bold">Class D and Class E : </h3>
              <p className="mt-2 text-justify">
                The Class D and Class E have the highest bits set to 1110 and
                1111 respectively. The Class D is used for multicasting and
                Class E is reserved for experimental use.
              </p>
            </li>
          </ul>

          <h3 className="font-bold mt-4">for example :</h3>
          <p className="mt-2 text-justify">
            Now, let us consider an IP address 192.168.1.34. Since the first
            octect lies in the decimal range 192-223, so the given IP address
            corresponds to Class C. In the Class C, the first three octects are
            reserved for Network address, so in the given IP address 192.168.1
            corresponds to the Netld. The maximum number of hosts that can be
            connected to such a network are 254 and the host that is currently
            attached to it has a host address 34.
          </p>

          <p className="mt-4 text-center text-xl">
            <span className="border-red-300 border">
            <span className="border-red-300 border">192 . 168 . 1 </span>
            <span> . 34</span>
            </span>
          </p>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const NETWORK_ADDRESS = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default NETWORK_ADDRESS;
