import React from "react";
import { useGloabalContext } from "./ContextAPI";
import INDIVIDUAL_BLOG_PAGE_COMPONENT from "../components/Blog_page";

const PAINTING_PAGE = () => {

    const {painting_page_blogs, s_text, pai_api, display_length} = useGloabalContext();

  return (
    <>
      <INDIVIDUAL_BLOG_PAGE_COMPONENT mov_pa_bl={painting_page_blogs} se_text={s_text.painting_page_text} page_api={pai_api} dis_length={display_length.pa_p_b} page_blog_type="PAINTING_PAGE_BLOG" page_name="Painting Blogs" seperate_page_for_search="PAINTING_PAGE"></INDIVIDUAL_BLOG_PAGE_COMPONENT>
    </>
  );
};

export default PAINTING_PAGE;
