import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import leader from "../images/leader.jpg";

const related_blogs = [
  {
    url: "/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness",
    title: "What is Physical Fitness and Components of Physical Fitness",
  },

  {
    url: "/education-blogs/importance-of-healthy-lifestyle",
    title: "Importance of Healthy Lifestyle",
  },

];

const Content = () => {
  const meta = {
    title: "Leadership and Qualities of Leader",
    description:
      "In this article you can read about Leadership and Qualities of Leader. Everybody wants to become a leader these days. He wants that people should listen to him. Thus he inculcates in himself those qualities which are essential to become a good leader.",
    canonical:
      "https://bangblogs.com/education-blogs/leadership-and-qualities-of-leader",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Leadership, Qualities of Leader, characterstics of Leader",
      },
      property: {
        og_title: "Leadership and Qualities of Leader",
        og_type: "article",
        og_url:
          "https://bangblogs.com/education-blogs/leadership-and-qualities-of-leader",
        og_description:
          "In this article you can read about Leadership and Qualities of Leader. Everybody wants to become a leader these days. He wants that people should listen to him. Thus he inculcates in himself those qualities which are essential to become a good leader.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Leadership and Qualities of Leader
        </h1>
        <div className=" p-5">
          <img
            src={leader}
            alt="leader"
          />
          <p className="text-justify mt-2">
            Everybody wants to become a leader these days. He wants that people
            should listen to him. Thus he inculcates in himself those qualities
            which are essential to become a good leader. People identify and
            respect him for these qualities only.
          </p>

          <h2 className="text-xl font-bold mt-4">Oualities of a Leader</h2>

          <ul className="list-[circle]">
            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Personality of a Leader :
              </h3>
              <p className="text-justify">
                The inner and outer Personalities of a good leader should be
                impressive. Inner personality includes the qualities of
                self-confidence, knowledge, experience, impartial behavior,
                decency, etc. Outer personality means that he should wear neat
                and clean clothes and should behave lovingly with everybody. The
                captain of the team and other players in games, play with each
                other and develop themselves.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Intelligent and Knowdledgeable :
              </h3>
              <p className="text-justify">
                A good leader should be intelligent also. He should have
                confidence in his knowledge and intelligence, A good player is
                always intelligent. He has to take many decisions while playing
                which depend on his knowledge of the game.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">Punctual :</h3>
              <p className="text-justify">
                A good leader should be punctual, he should respect the value of
                time. He should prepare beforehand his daily schedule so that he
                reaches everywhere in time. Sports competitions always begin on
                time. So the players should be punctual otherwise they are not
                allowed to participate. This develops in them the virtue of
                punctuality.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">Decisive :</h3>
              <p className="text-justify">
                A good leader should have the ability of taking an instant
                decision on the basis of available knowledge. He, therefore,
                depends upon his experience and knowledge for this. Players have
                to frequently change their strategies from time to time. They
                sometimes have to take instant decisions which makes them
                decisive.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Broad Minded and foresighted :
              </h3>
              <p className="text-justify">
                A good leader should always be prepared to learn something new.
                He should have the intellect to evaluate his plans and changes.
                Players are always ready to improve their level of performance.
                They are also ready to learn new techniques.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Disciplined and Honest :
              </h3>
              <p className="text-justify">
                A good leader should always be a role model for other he should
                never give a wrong decision. He should also not take the side of
                wrong person.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Courageous and Confident :
              </h3>
              <p className="text-justify">
                A good leader should have full confidence in his abilities. He
                should have the courage to face the truth. He should also be
                ready to accept and improve his shortcomings. A player lacking
                in self-confidence cannot perform his best. Thus a player always
                try to become courageous and confident.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">Unbiased :</h3>
              <p className="text-justify">
                A good leader should be unbiased. He should not be partial to
                anybody. He should treat everybody equally. An unbiased leader
                respects everybody. A good player is also unbiased. He praises
                only the good game.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">Good Orator :</h3>
              <p className="text-justify">
                Words are of great importance for men. A good leader should be a
                good orator as well. He can create new enthusiasm in the masses
                through his words. The words of praise act as stimulants for a
                player thus all players praise each other and raise the
                confidence of each other.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Sense of Humour
              </h3>
              <p className="text-justify">
                A good leader should not have coarse behavior. He should always
                try to cheer up his companions. He should not also leave courage
                in tough times. A leader keeps the morale of his fellow players
                high and stays happy even during difficult times.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Determination :
              </h3>
              <p className="text-justify">
                A good leader should have great determination so that he can
                face over all the hindrances that come his way.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Enthusiactic :
              </h3>
              <p className="text-justify">
                A good leader should always be enthusiastic about the cause of
                the people. He must have the capability to see what is good or
                bad for the people in the long run. He should approach a problem
                in a holistic manner and should never believe himself different
                from his people and subject.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const LEADERSHIP_QUALITIES = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default LEADERSHIP_QUALITIES;
