import blogs from "../API/api";

const fun = (api_cu, search_title) => {
  let count = 0;
  let ct = 0;
  let ok = false;
  for (let i = 0; i < search_title.length; i++) {
    if (search_title.length <= 3) {
      if (api_cu.query_str.toLowerCase().includes(search_title[i].toLowerCase())) {
        ok = true;
        break;
      } else {
        ok = false;
      }
    } else {
      if (api_cu.query_str.toLowerCase().includes(search_title[i].toLowerCase())) {
        ok = true;
        count++;
        if (count >= 2) {
          break;
        }
      } else {
        ok = false;
        ct++;
        if (ct >= 3) break;
      }
    }
  }
  if (ok) {
    return api_cu;
  }
};

const Reducer = (state, action) => {
  switch (action.type) {
    case "FILTER_SEARCH_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        fil_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_MOVIE_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        mov_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_other_category_blog_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        fas_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_TECHNOLOGY_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        tech_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_MOBILE_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        mob_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_SPORTS_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        spo_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_PAINTING_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        pai_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_EDUCATION_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        edu_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    case "FILTER_OTHER_BLOG":
      // console.log(action.payload);
      return {
        ...state,
        oth_api: blogs.filter((curEle) => {
          return fun(curEle, action.payload);
        }),
      };

    default:
  }
  return state;
};

export default Reducer;
