import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";
import Adaptive_phy_education from "../images/Adaptive_phy_education.jpg";

const related_blogs = [
  {
    url: "/education-blogs/leadership-and-qualities-of-leader",
    title: "Leadership and Qualities of Leader",
  },

  {
    url: "/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness", 
    title: "What is Physical Fitness and Components of Physical Fitness",
  },

  {
    url: "/education-blogs/importance-of-healthy-lifestyle",
    title: "Importance of Healthy Lifestyle",
  },
];

const Content = () => {
  const meta = {
    title:
      "What is Adaptive Physical Education and Aims or Objectives of Adaptive Physical Education",
    description:
      "In this article you can read about Adaptive Physical Education and Aims or Objectives of Adaptive Physical Education. Adaptive physical education is a subdiscipline of physical education that is adapted or modified for people who are differently abled, handicapped, mentally challenged, blind, etc.",
    canonical:
      "https://bangblogs.com/education-blogs/what-is-adaptive-physical-education-and-aims-or-objectives-of-adaptive-physical-education",
    meta: {
      charset: "utf-8",
      name: {
        keywords:
          "Adaptive Physical, Objectives of Adaptive Physical Education, Aims of Adaptive Physical Education",
      },
      property: {
        og_title:
          "What is Adaptive Physical Education and Aims or Objectives of Adaptive Physical Education",
        og_type: "article",
        og_url:
          "https://bangblogs.com/education-blogs/what-is-adaptive-physical-education-and-aims-or-objectives-of-adaptive-physical-education",
        og_description:
          "In this article you can read about Adaptive Physical Education and Aims or Objectives of Adaptive Physical Education. Adaptive physical education is a subdiscipline of physical education that is adapted or modified for people who are differently abled, handicapped, mentally challenged, blind, etc.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          What is Adaptive Physical Education and Aims or Objectives of Adaptive
          Physical Education
        </h1>
        <div className=" p-5">
          <img src={Adaptive_phy_education} alt="Adaptive Physical Education" />
          <p className="text-justify mt-2">
            Adaptive physical education is a subdiscipline of physical education
            that is adapted or modified for people who are differently abled,
            handicapped, mentally challenged, blind, etc. The programs of
            adaptive physical education involve specially designed programs of
            physical fitness, motor fitness, aquatic skills, dance skills,
            individual and group games, etc. to meet the unique need of
            individuals with disabilities. Such people may be provided modified
            sports and games when playing an important role in their lives i.e.
            recreation, fitness, development, moral values, etc.
          </p>

          <p className="text-justify mt-2">
            In adaptive physical education, educators work with students,
            parents, and professionals in various disciplines to design training
            programs to meet individualized needs. Adaptive physical education
            programs have different physical activities for disabled people for
            example wheelchair is used for leg-handicapped people to participate
            in races and a sound ball is used for blind people to participate in
            throwing ball games.
          </p>

          <h2 className="text-xl font-bold mt-4">
            Aims and Objectives of Adaptive Physical Education
          </h2>
          <p className="text-justify mt-2">
            The aim of Adaptive Physical Education is to provide all students
            with special needs, regardless of their disability, the opportunity
            to participate in physical education, safely and successfully, in
            the least restrictive environment. It helps the students to achieve
            physical, mental, emotional, and social growth. Adaptive Physical
            Education also helps to develop and build up self-esteem in the
            students which is an essential component of personality development
          </p>

          <ul className="list-[circle] ml-10">
            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Provide Physical Education Services :{" "}
              </h3>
              <p className="text-justify">
                The primary objective of adapted physical education is to
                provide children with physical education services that meet
                their unique needs. The safety of the child is the main
                priority.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Development of Self Esteem :{" "}
              </h3>
              <p className="text-justify">
                The development of self-esteem is also one of the objectives o
                the adaptive physical education program. Once the development of
                enhanced sole esteem is achieved, the students are more eager to
                participate in more challenging activities that often result in
                improved motor development.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Development of Motor Skills :{" "}
              </h3>
              <p className="text-justify">
                Another objective is the development of the student's motor
                skills. It helps in developing the physical skills and abilities
                of the child.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Development of Physical Fitness :{" "}
              </h3>
              <p className="text-justify">
                Adaptive physical education provides knowledge about modified
                exercises suitable for persons with disability and helps in
                developing the physical fitness of these students.
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Development of Socialisation Skills :{" "}
              </h3>
              <p className="text-justify">
                The development of socialization skills is also the objective of
                adaptive physical education programs. Listening and following
                directions, adequately participating in groups, staying vigilant
                in the work and the organization of work materials is certain
                behaviors that are known as "social skills related to learning".
              </p>
            </li>

            <li>
              <h3 className="font-bold text-[18px] mt-2 mb-2">
                Motivate Students :{" "}
              </h3>
              <p className="text-justify">
                Another important objective of adaptive physical education is to
                motivate the students, boost their morale and prevent them from
                any discrimination in society.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT
          api_name={related_blogs}
        ></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const ADAPTIVE_PHYSICAL_EDUCATION = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default ADAPTIVE_PHYSICAL_EDUCATION;
