import React from "react";
import DocumentMeta from "react-document-meta";
import BLOG_TEMPLATE from "../blog_template/Blog_template";
import healthy_l_sty from "../images/healthy_life_style.jpg";
import abilities from "../images/abilites.jpg";
import reduces_health_problem from "../images/reduces_health_problem.jpg";
import self_confidence from "../images/self_confidence.jpg";
import level_of_energy from "../images/level_of_energy.jpg";
import posture from "../images/posture.jpg";
import depression from "../images/depression.jpg";
import better_stress_management from "../images/better_stress_management.jpg";
import RELATED_BLOG_COMPONENT from "../components/Related_blog_component";


const related_blogs = [
  {
    url: "/education-blogs/What-is-physical-fitness-and-components-of-physical-fitness",
    title: "What is Physical Fitness and Components of Physical Fitness",
  },

  {
    url: "/education-blogs/importance-of-wellness",
    title: "Importance of Wellness",
  },
];


const Content = () => {
  const meta = {
    title: "Importance of Healthy LifeStyle",
    description:
      "In this article you can read about  Importance of Healthy Lifestyle. Living a healthy lifestyle plays a vital role in improving our abilities.",
    canonical: "https://bangblogs.com/education-blogs/importance-of-healthy-lifestyle",
    meta: {
      charset: "utf-8",
      name: {
        keywords: "Healthy LifeStyle, better life style, importance of healthy lifestyle",
      },
      property: {
        og_title: "Importance of Healthy LifeStyle",
        og_type: "article",
        og_url: "https://bangblogs.com/education-blogs/importance-of-healthy-lifestyle",
        og_description:
          "In this article you can read about  Importance of Healthy Lifestyle. Living a healthy lifestyle plays a vital role in improving our abilities.",
        og_site_name: "bangblogs",
      },
    },
  };

  return (
    <>
      <DocumentMeta {...meta}>
        <h1 className="text-2xl font-bold text-center">
          Importance of Healthy Lifestyle
        </h1>
        <div className=" p-5">
          <img src={healthy_l_sty} alt="healthy life style" className="" />
          <ul className="list-[circle]">
            <li className="mt-4">
              <h2 className="text-xl font-bold">Improvement in Abilities : </h2>
              <img
                src={abilities}
                alt="healthy active living"
                className="mt-2"
              />
              <p className="mt-2 text-justify">
                Living a healthy lifestyle plays a vital role in improving our
                abilities. Our strength, speed, flexibility, endurance,
                coordination, and reaction time, all abilities are enhanced to a
                great extent.
              </p>
            </li>
            <li className="mt-4">
              <h2 className="text-xl font-bold">Reduces Health Problems : </h2>
              <img
                src={reduces_health_problem}
                alt="reduces health problem"
                className="mt-2"
              />
              <p className="mt-2 text-justify">
                A healthy lifestyle prevents us from various diseases like
                cardiac disorders, diabetes, cancer, asthma, obesity, and many
                more chronic problems. It helps in reducing extra body fat and
                strengthens the heart, resulting in more blood and oxygen flow
                throughout the body. It also improves the immune system and
                reduces the risk of minor ailments.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Better Management of Stress and Tension :{" "}
              </h2>
              <img src={better_stress_management} alt="better stress management" className="mt-2" />
              <p className="mt-2 text-justify">
                Emotional stress leads to anxiety and then to depression. This
                can lead to other problems like loss of appetite, insomnia, and
                restlessness. A Healthy Lifestyle helps in better management of
                stress and reducing depression. An active and healthy lifestyle
                always keeps the person believing in himself and his abilities
                and keeps him emotionally stable.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Increases Self-Confidence :{" "}
              </h2>
              <img src={self_confidence} alt="self confidence" className="mt-2" />
              <p className="mt-2 text-justify">
                A healthy lifestyle increases the level of fitness of an
                individual. After getting fit, an individual tends to become
                more self-confident and self-aware.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Increases the Level of Energy :{" "}
              </h2>
              <img src={level_of_energy} alt="increase level of energy" className="mt-2" />
              <p className="mt-2 text-justify">
                A healthy lifestyle increases the level of energy. An individual
                with a healthy lifestyle feels more energetic which means more
                productivity in day-to-day living. With the increased level of
                energy, we can accomplish more work in our life which in turn
                provides a greater sense of satisfaction.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">Better Posture : </h2>
              <img src={posture} alt="better posture" className="mt-2" />
              <p className="mt-2 text-justify">
                A healthy lifestyle helps to maintain the correct posture which
                prevents injuries and other problems that can happen due to poor
                posture.
              </p>
            </li>

            <li className="mt-4">
              <h2 className="text-xl font-bold">
                Helps to Reduce Depression :{" "}
              </h2>
              <img src={depression} alt="reduce depression" className="mt-2" />
              <p className="mt-2 text-justify">
                A healthy mind lives in a healthy body. A healthy lifestyle
                increases positivity and reduces negative thoughts thus helping
                to reduce depression.
              </p>
            </li>
          </ul>
        </div>
        <RELATED_BLOG_COMPONENT api_name={related_blogs}></RELATED_BLOG_COMPONENT>
      </DocumentMeta>
    </>
  );
};

const IMPORTANCE_OF_HEALTHY_LIFESTYLE = () => {
  return (
    <BLOG_TEMPLATE>
      <Content></Content>
    </BLOG_TEMPLATE>
  );
};

export default IMPORTANCE_OF_HEALTHY_LIFESTYLE;
